/**
 * 
 * TODO gltf
 * @param url 
 * @param onLoad 
 * @param onError 
 */
export function loadGltf(
	url: string,
	onLoad: (gltf: GltfData) => void,
	onError: (err: any) => void
) {
	let callback = (s: boolean, res: any) => {
		if (s) {//
			try {
				var content: string;
				var extensions = {};
				if (typeof res === 'string') {
					content = res;
				} else {
					var magic = decodeText(new Uint8Array(res, 0, 4));
					if (magic === BINARY_EXTENSION_HEADER_MAGIC) {
						try {
							extensions[EXTENSIONS.KHR_BINARY_GLTF] = new GLTFBinaryExtension(res);
						} catch (error) {
							if (onError) onError(error);
							return;
						}
						content = extensions[EXTENSIONS.KHR_BINARY_GLTF].content;
					} else {
						content = decodeText(new Uint8Array(res));
					}
				}
				var json = JSON.parse(content);
				// console.log(json)
				if (json.asset === undefined || json.asset.version[0] < 2) {
					onError && onError("")
					return;
				}
				var parser = new GLTFParser(json, extensions);

				parser.parse(function (scene, scenes, cameras, animations, json) {
					const obj: any = {
						scene,
						scenes,
						cameras,
						animations,
						asset: json.asset,
						parser,
						// userData: {}
					}
					//
					if (animations && animations.length) obj.animationManager = new AnimationManager(animations);
					onLoad(obj)
				}, onError);
			} catch (e) {
				onError && onError(e);
			}
		} else {
			onError && onError(res)
		}
	}
	// if (getEnv() == "tb") {
	// 	GlobalLoader.tbLoad(callback, url, "ArrayBuffer")
	// } else {
	// 	GlobalLoader.loadRawWeb(callback, url, "arraybuffer")
	// }
	GlobalLoader.loadAB(callback, url);
}
