import BaseTexture from "./BaseTexture";
import { Rectangle } from "../math";
import Texture from "./Texture";

interface dataTm {
    x: number,  //x,y,w,h
    y: number,
    w: number,
    h: number,
    ox: number,  //ox,oytrimorig{x:0,y:0,width:200,height:200}
    oy: number,
    sw: number, //sw,sh
    sh: number,
    ro: boolean, //
}
//
var expData = {
    "actOverTitle.png": { "x": 1, "y": 1, "w": 474, "h": 172, "ox": 0, "oy": 0, "sw": 474, "sh": 172, "ro": false },
    "hasActOverLotBtn.png": { "x": 1, "y": 175, "w": 259, "h": 85, "ox": 0, "oy": 0, "sw": 259, "sh": 85, "ro": false }
}

/**
 * 
 * @param baseTexture 
 * @param altaData 
 * @return 
 */
export function createTextureSheet(baseTexture: BaseTexture, altaData): { [key: string]: Texture } {
    var frames = altaData;
    var frameKeys = Object.keys(frames);
    let frameIndex = 0;
    //
    var textures: { [key: string]: Texture } = {};

    while (frameIndex < frameKeys.length) {
        //
        const i = frameKeys[frameIndex];
        //
        const data: dataTm = frames[i];
        //
        let frame: Rectangle = null;
        //
        let trim: Rectangle = null;
        //
        const orig = new Rectangle(
            0,
            0,
            Math.floor(data.sw),
            Math.floor(data.sh)
        );

        //
        if (data.ro) {
            frame = new Rectangle(
                Math.floor(data.x),
                Math.floor(data.y),
                Math.floor(data.h),
                Math.floor(data.w)
            );
        } else {
            frame = new Rectangle(
                Math.floor(data.x),
                Math.floor(data.y),
                Math.floor(data.w),
                Math.floor(data.h)
            );
        }
        // ,,
        if (data.ox || data.oy || data.w != data.sw || data.h != data.sh) {
            //orig
            trim = new Rectangle(
                Math.floor(data.ox),
                Math.floor(data.oy),
                Math.floor(data.w),
                Math.floor(data.h)
            );
        }

        var texture = new Texture(
            baseTexture,
            frame,
            orig,
            trim,
            data.ro ? 2 : 0,
            // data.anchor
        );
        //
        Texture.addToCache(texture, i);
        textures[i] = texture;
        frameIndex++;
    }

    return textures;
}