import { EventDispatcher } from '../events/EventDispatcher';
import { isPow2, BaseTextureCache, TextureCache, uid } from '../utils';
import { SCALE_MODES, WRAP_MODES } from "../const"
import { GlobalLoader } from '../loader';

/**
 * textureBaseTexturetexture
 * BaseTexture
 * @class
 * @extends EventDispatcher
 */
export default class BaseTexture extends EventDispatcher {
    /**
     * 
     * 
     */
    _touchedId: number;
    /**
     * 
     * id
     */
    _enabledId: number;
    /**
     * 
     */
    width: number;
    /**
     * 
     */
    height: number;
    /**
     * SCALE_MODES
     * glTexture
     */
    scaleMode: SCALE_MODES;
    /**
     * true
     * false
     * 
     */
    hasLoaded: boolean;
    /**
     * 
     */
    private _isLoading: boolean;
    /**
     * image eg. `png`
     * svg
     * @readonly
     */
    imageType: string;
    /**
     * rgbalphawebglpngtrue
     * @default true
     */
    premultipliedAlpha: boolean;
    /**
     * 
     * @member {string}
     */
    imageUrl: string;
    /**
     * 22gpumipmap
     */
    isPowerOfTwo: boolean;
    /**
     * 2truemipmap
     * @default true
     */
    mipmap: boolean;
    /**
     * 2CLAMP
     * WebGL Texture wrap mode
     * @default WRAP_MODES.CLAMP
     */
    wrapMode: WRAP_MODES;
    /**
     * A map of renderer IDs to webgl textures
     * 
     * @member {object<number, WebGLTexture>}
     */
    _glTextures: {};
    /**
     * The ids under which this BaseTexture has been added to the base texture cache. This is
     * automatically set as long as BaseTexture.addToCache is used, but may not be set if a
     * BaseTexture is added directly to the BaseTextureCache array.
     * @member {string[]}
     */
    textureCacheIds: string[];

    /**
     * 
     * imageDatacanvas;
   * {
   * data:Uint8Array,
   * width:number,
   * height:number,
   * type:string,
   * path:string
   * }
   * canvas.createImage()source.data
   * canvas
     */
    source;


    /**
     * @param {} [source] - 
     * @param {number} - possible values
     */
    constructor(source = null, scaleMode: SCALE_MODES = SCALE_MODES.LINEAR) {
        super();
        this._touchedId = 0;
        this.width = 100;
        this.height = 100;
        this.scaleMode = scaleMode;
        this.hasLoaded = false;
        this._isLoading = false;
        this.source = null;
        this.imageType = null;
        this.premultipliedAlpha = true;
        this.imageUrl = null;
        this.isPowerOfTwo = false;
        this.mipmap = true;
        this.wrapMode = WRAP_MODES.CLAMP;
        this._glTextures = {};
        this._enabledId = 0;
        this.textureCacheIds = [];
        //sourcebaseTexturefromUrl
        if (source) {
            //imageData
            if (source.data) {
                this._sourceChange(source);
            }
            //canvas
            else if (source.getContext) {
                this._sourceChange(source);
            }
            else {
                if (source.complete) {
                    this._sourceChange(source);
                    this.dispatchEvent("loaded");
                } else {
                    var self = this
                    source.onload = function () {
                        self._sourceChange(source);
                        self.dispatchEvent("loaded");
                    }
                }
            }
        }
    }

    /**
     * 
     * @fires BaseTexture#update
     */
    update() {
        //
        this.width = this.source.width;
        this.height = this.source.height;
        //
        this.hasLoaded = this.width && this.height ? true : false;
        //2n
        this.isPowerOfTwo = isPow2(this.width) && isPow2(this.height);

        //webgl
        this.dispatchEvent("update")
    }

    /**
     * 
     */
    _sourceChange(source) {
        //
        this.source = source;
        //src path
        this.imageUrl = source.src || source.path || null;
        //,
        this.imageType = this.source.type || null;
        //
        this.hasLoaded = true;
        //
        this.update();
        //
        this.dispatchEvent("loaded");
    }

    /**
     *  base texture
     * 
     */
    destroy() {
        if (this.imageUrl) {
            delete TextureCache[this.imageUrl];
            this.imageUrl = null;
        }
        this.source = null;
        this.dispose();
        BaseTexture.removeFromCache(this);
        this.textureCacheIds = null;
    }

    /**
     * gpuGPU
     * @fires BaseTexture#dispose
     */
    dispose() {
        //TextureManager
        this.dispatchEvent("dispose")
    }

    //
    /**
     * 
     * @param {string} url  
     */
    static fromUrl(url: string) {
        if (BaseTextureCache[url]) return BaseTextureCache[url];
        let baseTexture = new BaseTexture();
        GlobalLoader.loadImage((s, res) => {
            if (s) {
                baseTexture._sourceChange(res);
                //
                if (url.indexOf('data:') !== 0) BaseTexture.addToCache(baseTexture, url);
            }
        }, url)
        return baseTexture
    }

    /**
     * data
     * @param data 
     */
    static fromData(data) {
        return new BaseTexture(data);
    }

    /**
     * canvas
     */
    static fromCanvas(canvas: HTMLCanvasElement, origin: string = 'canvas') {
        //canvasId
        if (!canvas["_canvasId"]) {
            canvas["_canvasId"] = `${origin}_${uid()}`;
        }
        let baseTexture = BaseTextureCache[canvas["_canvasId"]];
        if (!baseTexture) {
            baseTexture = new BaseTexture(canvas);
            BaseTexture.addToCache(baseTexture, canvas["_canvasId"]);
        }
        return baseTexture;
    }
    /**
     * 
     * @param image 
     */
    static fromImage(image: HTMLImageElement) {
        //
        const imageUrl = image.src;
        let baseTexture = BaseTextureCache[imageUrl];
        if (!baseTexture) {
            baseTexture = new BaseTexture(image);
            //base64
            if (imageUrl && imageUrl.indexOf('data:') !== 0) BaseTexture.addToCache(baseTexture, imageUrl);
        }
        return baseTexture;
    }
    //datadata
    static from(anything: string | HTMLCanvasElement | HTMLImageElement) {
        //
        if (typeof anything === 'string') {
            return BaseTexture.fromUrl(anything);
        }
        //@ts-ignore
        else if (anything.data) {
            return BaseTexture.fromData(anything);
        }
        //@ts-ignore canvas
        else if (anything.getContext) {
            //@ts-ignore
            return BaseTexture.fromCanvas(anything);
        } else {
            //@ts-ignore
            return BaseTexture.fromImage(anything);
        }
    }

    /**
     * 
     * @static
     * @param {BaseTexture} baseTexture
     * @param {string} id
     */
    static addToCache(baseTexture: BaseTexture, id: string) {
        if (id) {
            if (baseTexture.textureCacheIds.indexOf(id) === -1) {
                baseTexture.textureCacheIds.push(id);
            }

            if (BaseTextureCache[id]) {
                //
                console.warn(`BaseTexture added to the cache with an id [${id}] that already had an entry`);
            }
            BaseTextureCache[id] = baseTexture;
        }
    }

    /**
     * 
     * @static
     * @param {string|BaseTexture} baseTexture idBaseTexture
     * @return {BaseTexture|null} BaseTexturenull
     */
    static removeFromCache(baseTexture: string | BaseTexture): BaseTexture | null {
        if (typeof baseTexture === 'string') {
            const baseTextureFromCache = BaseTextureCache[baseTexture];
            if (baseTextureFromCache) {
                const index = baseTextureFromCache.textureCacheIds.indexOf(baseTexture);
                if (index > -1) {
                    baseTextureFromCache.textureCacheIds.splice(index, 1);
                }
                delete BaseTextureCache[baseTexture];
                return baseTextureFromCache;
            }
        }
        else if (baseTexture && baseTexture.textureCacheIds) {
            for (let i = 0; i < baseTexture.textureCacheIds.length; ++i) {
                delete BaseTextureCache[baseTexture.textureCacheIds[i]];
            }
            baseTexture.textureCacheIds.length = 0;
            return baseTexture;
        }
        return null;
    }
}
