import { HashObject } from "../HashObject";
/**
 * @class Point
 * @extends HashObject
 * @since 1.0.0
 * @public
 */
export class Point extends HashObject {
    public destroy(): void { }
    /**
     * 
     * @method Point
     * @public
     * @since 1.0.0
     * @param x
     * @param y
     */
    constructor(x: number = 0, y: number = 0) {
        super();
        let s = this;
        s._instanceType = "Point";
        s.x = x;
        s.y = y;
    }
    /**
     * 
     * @property x
     * @public
     * @since 1.0.0
     * @type{number}
     */
    public x: number = 0;
    /**
     * 
     * @property y
     * @since 1.0.0
     * @public
     * @type {number}
     */
    public y: number = 0;
    /**
     * 
     * @method distance
     * @param args  Point x y x y
     * @return {number}
     * @static
     */
    public static distance(...args: any[]): number {
        let len = args.length;
        if (len == 4) {
            return Math.sqrt((args[0] - args[2]) * (args[0] - args[2]) + (args[1] - args[3]) * (args[1] - args[3]));
        } else if (len == 2) {
            return Math.sqrt((args[0].x - args[1].x) * (args[0].x - args[1].x) + (args[0].y - args[1].y) * (args[0].y - args[1].y));
        }
    }

    set(x: number, y: number) {
        this.x = x;
        this.y = y;
    }
}
