import Texture from "../texture/Texture";
import { SCALE_MODES, LINE_CAP, LINE_JOIN } from "../const";
import { Rectangle, Point } from "../math";
import { DisplayObject } from "../display/DisplayObject";
import { hex2string, getRGBA, getGradientColor, getBackupCanvasCtx, getCanvasBitmapStyle, createCanvas, string2hex } from "../utils";
import Sprite from "../display/Sprite";

/**
 * shapeshapespritehittest
 * 
 * canvas
 * canvasapi
 * patternGraphic
 * webglgracanvas
 * 
 * anchorTexture
 * anchor
 * @class Shape
 * @extends Sprite
 */
export class Shape extends Sprite {
    canvas: HTMLCanvasElement;
    context: CanvasRenderingContext2D;
    dirty: boolean;
    offsetX: number;
    offsetY: number;
    constructor() {
        super();
        this._instanceType = "Shape";
        var canvas = createCanvas();
        canvas.width = 3;
        canvas.height = 3;
        this.texture = Texture.fromCanvas(canvas, 'shapeCanvas');

        //baseTexturetexturekey textCanvas+num  baseTexture
        Texture.addToCache(this.texture, this.texture.baseTexture.textureCacheIds[0]);
        this.canvas = canvas;
        this.context = canvas.getContext("2d");
        this.dirty = true;

    }

    /**
     * [ 0,1, ,]
     * 
     * @property _command
     * @private
     * @since 1.0.0
     * @type {Array}
     * @default []
     */
    private _command: any = [];

    /**
     * @property _isBitmapStroke
     * @private
     * @since 1.0.0
     */
    private _isBitmapStroke: Array<number>;
    /**
     * @property _isBitmapFill
     * @private
     * @since 1.0.0
     */
    private _isBitmapFill: Array<number>;
    /**
     *  
     * @property hitTestByPixel
     * @type {boolean}
     * @default true
     * @since 1.1.0
     */
    public hitTestByPixel: boolean = true;


    /**
     * 
     * @method drawRoundedRect
     * @param {number} x x
     * @param {number} y y
     * @param {number} w 
     * @param {number} h 
     * @param {number} rTL 
     * @param {number} rTR 
     * @param {number} rBL 
     * @param {number} rBR 
     * @public
     * @since 1.0.0
     */
    public drawRoundedRect(
        x: number,
        y: number,
        w: number,
        h: number,
        rTL: number = 0,
        rTR?: number,
        rBL?: number,
        rBR?: number
    ): this {
        let max = (w < h ? w : h) / 2;
        let mTL = 0, mTR = 0, mBR = 0, mBL = 0;
        if (rTR == undefined) rTR = rTL;
        if (rBL == undefined) rBL = rTL;
        if (rBR == undefined) rBR = rTL;
        if (rTL < 0) {
            rTL *= (mTL = -1);
        }
        if (rTL > max) {
            rTL = max;
        }
        if (rTR < 0) {
            rTR *= (mTR = -1);
        }
        if (rTR > max) {
            rTR = max;
        }
        if (rBR < 0) {
            rBR *= (mBR = -1);
        }
        if (rBR > max) {
            rBR = max;
        }
        if (rBL < 0) {
            rBL *= (mBL = -1);
        }
        if (rBL > max) {
            rBL = max;
        }
        let c = this._command;
        c[c.length] = [1, "moveTo", [x + w - rTR, y]];
        c[c.length] = [1, "arcTo", [x + w + rTR * mTR, y - rTR * mTR, x + w, y + rTR, rTR]];
        c[c.length] = [1, "lineTo", [x + w, y + h - rBR]];
        c[c.length] = [1, "arcTo", [x + w + rBR * mBR, y + h + rBR * mBR, x + w - rBR, y + h, rBR]];
        c[c.length] = [1, "lineTo", [x + rBL, y + h]];
        c[c.length] = [1, "arcTo", [x - rBL * mBL, y + h + rBL * mBL, x, y + h - rBL, rBL]];
        c[c.length] = [1, "lineTo", [x, y + rTL]];
        c[c.length] = [1, "arcTo", [x - rTL * mTL, y - rTL * mTL, x + rTL, y, rTL]];
        c[c.length] = [1, "closePath", []];

        return this
    }

    /**
     * 
     * @method moveTo
     * @param {number} x
     * @param {number} y
     * @public
     * @since 1.0.0
     */
    public moveTo(x: number, y: number): this {
        this._command[this._command.length] = [1, "moveTo", [x, y]];

        return this;
    }

    /**
     * ,(0,0)
     * @method lineTo
     * @param {number} x
     * @param {number} y
     * @public
     * @since 1.0.0
     */
    public lineTo(x: number, y: number): this {
        this._command[this._command.length] = [1, "lineTo", [x, y]];
        return this;
    }

    /**
     * 
     * https://www.jianshu.com/p/e8ea5996cd79
     * @method arcTo
     * @param {number} x1 
     * @param {number} y1
     * @param {number} x2  
     * @param {number} y2 
     * @param {number} radius 
     * @public
     * @since 1.0.0
     */
    public arcTo(x1: number, y1: number, x2: number, y2: number, radius: number): this {
        this._command[this._command.length] = [1, "arcTo", [x1, y1, x2, y2, radius]];
        return this;
    }

    /**
     * 
     * ,(0,0)
     * @method quadraticCurveTo
     * @param {number} cpX X
     * @param {number} cpX Y
     * @param {number} x X
     * @param {number} y Y
     * @public
     * @since 1.0.0
     */
    public quadraticCurveTo(cpX: number, cpY: number, x: number, y: number): this {
        this._command[this._command.length] = [1, "quadraticCurveTo", [cpX, cpY, x, y]];
        return this;
    }

    /**
     * 
     * ,(0,0)
     * @method bezierCurveTo
     * @param {number} cp1X 1X
     * @param {number} cp1Y 1Y
     * @param {number} cp2X 2X
     * @param {number} cp2Y 2Y
     * @param {number} x X
     * @param {number} y Y
     * @public
     * @since 1.0.0
     */
    public bezierCurveTo(cp1X: number, cp1Y: number, cp2X: number, cp2Y: number, x: number, y: number): this {
        this._command[this._command.length] = [1, "bezierCurveTo", [cp1X, cp1Y, cp2X, cp2Y, x, y]];
        return this;
    }

    /**
     * 
     * @method closePath
     * @public
     * @since 1.0.0
     */
    public closePath(): this {
        this._command[this._command.length] = [1, "closePath", []];
        return this;
    }

    /**
     * 
     * @method drawRect
     * @param {number} x
     * @param {number} y
     * @param {number} w
     * @param {number} h
     * @public
     * @since 1.0.0
     */
    public drawRect(x: number, y: number, w: number, h: number): this {
        let c = this._command;
        c[c.length] = [1, "moveTo", [x, y]];
        c[c.length] = [1, "lineTo", [x + w, y]];
        c[c.length] = [1, "lineTo", [x + w, y + h]];
        c[c.length] = [1, "lineTo", [x, y + h]];
        c[c.length] = [1, "closePath", []];
        return this;
    }

    /**
     * 
     * https://www.w3school.com.cn/tags/canvas_arc.asp
     * 
     * @method drawArc
     * @param {number} x x
     * @param {number} y y
     * @param {number} radius 
     * @param {number} start  
     * @param {number} end  
     * @param {number} counterclockwise False = true = 
     * @public
     * @since 1.0.0
     */
    public arc(
        x: number,
        y: number,
        radius: number,
        startAngle: number,
        endAngle: number,
        counterclockwise: boolean = false
    ): this {
        this._command[this._command.length] = [1, "arc", [x, y, radius, startAngle, endAngle, counterclockwise]];
        return this;
    }

    /**
     * 
     * @method drawCircle
     * @param {number} x x
     * @param {number} y y
     * @param {number} radius 
     * @param {boolean} counterclockwise 
     * @public
     * @since 1.0.0
     */
    public drawCircle(x: number, y: number, radius: number, counterclockwise: boolean = false): this {
        this._command[this._command.length] = [1, "arc", [x, y, radius, 0, 2 * Math.PI, counterclockwise]];
        return this;
    }

    /**
     * 
     * @method drawEllipse
     * @param {number} x
     * @param {number} y
     * @param {number} w
     * @param {number} h
     * @public
     * @since 1.0.0
     */
    public drawEllipse(x: number, y: number, w: number, h: number): this {
        let k = 0.5522848;
        let ox = (w / 2) * k;
        let oy = (h / 2) * k;
        let xe = x + w;
        let ye = y + h;
        let xm = x + w / 2;
        let ym = y + h / 2;
        let c = this._command;
        c[c.length] = [1, "moveTo", [x, ym]];
        c[c.length] = [1, "bezierCurveTo", [x, ym - oy, xm - ox, y, xm, y]];
        c[c.length] = [1, "bezierCurveTo", [xm + ox, y, xe, ym - oy, xe, ym]];
        c[c.length] = [1, "bezierCurveTo", [xe, ym + oy, xm + ox, ye, xm, ye]];
        c[c.length] = [1, "bezierCurveTo", [xm - ox, ye, x, ym + oy, x, ym]];
        return this;
    }

    /**
     * 
     * @method clear
     * @public
     * @since 1.0.0
     */
    public clear(): this {
        let s = this;
        s._command = [];
        s.dirty = true;
        return s;
    }

    /**
     * ,,
     * @method beginFill
     * @param {number|string} color  RGBA
     * @public
     * @since 1.0.0
     */
    public beginFill(color: number | string = 0, alpha: number = 1): this {
        //
        if (typeof (color) == "string") color = string2hex(color);
        
        var sColor = getRGBA(hex2string(color), alpha);
        this._fill(sColor);
        return this;
    }

    /**
     * 
     * @method beginGradientFill
     * @param {Array} points createLinearGradientcreateRadialGradient
     * @param {Array} colors [, #, ][[0, "#ff0000", 1],[0.5, "#00ff00", 1],[1, "#0000ff", 1]]
     * @public
     * @since 1.0.0
     */
    public beginGradientFill(points: number[], colors: [number, string, number][]): this {
        this._fill(getGradientColor(points, colors));
        return this;
    }


    /**
     * 
     * @method beginBitmapFill
     * @param {Image} image
     * @param { Array} matrix
     * @public
     * @since 1.0.0
     */
    public beginBitmapFill(image: any, matrix: Array<number>): this {
        let s = this;
        if (matrix) {
            s._isBitmapFill = matrix;
        }
        s._fill(getCanvasBitmapStyle(image));
        return s;
    }

    private _fill(fillStyle: any): void {
        let c = this._command;
        c[c.length] = [0, "fillStyle", fillStyle];
        c[c.length] = [1, "beginPath", []];
        this.dirty = true;
    }

    /**
     * 
     * @method beginStroke
     * @param {number} color  
     * @param {number} lineWidth 
     * @param {number} cap  0 butt 1 round 2 square  butt
     * @param {number} join  0 miter 1 bevel 2 round  miter
     * @param {number} miter ,, miterLimit  lineJoin  "bevel"  10
     * @public
     * @since 1.0.0
     */
    public beginStroke(
        color: number,
        lineWidth: number = 1,
        cap: LINE_CAP = LINE_CAP.BUTT,
        join: LINE_JOIN = LINE_JOIN.MITER,
        miter: number = 0,
        alpha: number = 1
    ): this {
        var sColor = getRGBA(hex2string(color), alpha);
        this._stroke(sColor, lineWidth, cap, join, miter);
        return this;
    }

    /**
     * 
     * @method beginGradientStroke
     * @param {Array} points createLinearGradientcreateRadialGradient
     * @param {Array} colors [, #, ][[0, "#ff0000", 1],[0.5, "#00ff00", 1],[1, "#0000ff", 1]]
     * @param {number} lineWidth
     * @param {number} cap  0 butt 1 round 2 square  butt
     * @param {number} join  0 miter 1 bevel 2 round  miter
     * @param {number} miter ,, miterLimit  lineJoin  "bevel"  10
     * @public
     * @since 1.0.0
     */
    public beginGradientStroke(
        points: Array<number>,
        colors: any,
        lineWidth: number = 1,
        cap: LINE_CAP = LINE_CAP.BUTT,
        join: LINE_JOIN = LINE_JOIN.MITER,
        miter: number = 10
    ): this {
        this._stroke(getGradientColor(points, colors), lineWidth, cap, join, miter);
        return this;
    }

    /**
     *  Flash2x
     * @method beginBitmapStroke
     * @param {Image} image
     * @param {Array} matrix
     * @param {number} lineWidth
     * @param {string} cap  butt round square  butt
     * @param {string} join  bevel round miter miter
     * @param {number} miter ,, miterLimit  lineJoin  "bevel"  10
     * @public
     * @since 1.0.0
     */
    public beginBitmapStroke(
        image: any,
        matrix: Array<number>,
        lineWidth: number = 1,
        cap: LINE_CAP = LINE_CAP.BUTT,
        join: LINE_JOIN = LINE_JOIN.MITER,
        miter: number = 10
    ): this {
        let s = this;
        if (matrix) {
            s._isBitmapStroke = matrix;
        }
        s._stroke(getCanvasBitmapStyle(image), lineWidth, cap, join, miter);
        return s;
    }

    /**
     * @method _stroke
     * @param strokeStyle
     * @param {number} width
     * @param {number} cap
     * @param {number} join
     * @param {number} miter
     * @private
     * @since 1.0.0
     */
    private _stroke(strokeStyle: any, width: number, cap: LINE_CAP, join: LINE_JOIN, miter: number): void {
        let c = this._command;
        c[c.length] = [0, "lineWidth", width];
        c[c.length] = [0, "lineCap", cap];
        c[c.length] = [0, "lineJoin", join];
        c[c.length] = [0, "miterLimit", miter];
        c[c.length] = [0, "strokeStyle", strokeStyle];
        c[c.length] = [1, "beginPath", []];
        this.dirty = true;
    }

    /**
     * 
     * @method endFill
     * @public
     * @since 1.0.0
     */
    public endFill(): this {
        let s = this;
        let c = s._command;
        let m = s._isBitmapFill;
        if (m) {
            c[c.length] = [2, "setTransform", m];
        }
        c[c.length] = ([1, "fill", []]);
        if (m) {
            s._isBitmapFill = null;
        }
        return s;
    }
    // protected isUsedToMask: boolean = false;
    /**
     * 
     * @method endStroke
     * @public
     * @since 1.0.0
     */
    public endStroke(): this {
        let s = this;
        let c = s._command;
        let m = s._isBitmapStroke;
        if (m) {
            //2
            c[c.length] = [2, "setTransform", m];
        }
        c[c.length] = ([1, "stroke", []]);
        if (m) {
            s._isBitmapStroke = null;
        }
        return s;
    }

    /**
     * 
     * @method update
     * @public
     * @since 1.0.0
     */
    public updateShape(): void {
        let s: Shape = this;
        if (!s.visible) return;
        if (!s.dirty) return;
        s.dirty = false;
        s._boundsID++;//ContainercalculateBounds;
        //
        let cLen: number = s._command.length;
        let leftX: number;
        let leftY: number;
        let buttonRightX: number;
        let buttonRightY: number;
        let i: number;
        //,
        let data: any;
        let lastX = 0;
        let lastY = 0;
        let lineWidth = 0;
        for (i = 0; i < cLen; i++) {
            data = s._command[i];
            if (data[0] == 1) {
                if (data[1] == "moveTo" || data[1] == "lineTo" || data[1] == "arcTo" || data[1] == "bezierCurveTo") {
                    if (leftX == undefined) {
                        leftX = data[2][0];
                    }
                    if (leftY == undefined) {
                        leftY = data[2][1];
                    }
                    if (buttonRightX == undefined) {
                        buttonRightX = data[2][0];
                    }
                    if (buttonRightY == undefined) {
                        buttonRightY = data[2][1];
                    }

                    if (data[1] == "bezierCurveTo") {
                        leftX = Math.min(leftX, data[2][0], data[2][2], data[2][4]);
                        leftY = Math.min(leftY, data[2][1], data[2][3], data[2][5]);
                        buttonRightX = Math.max(buttonRightX, data[2][0], data[2][2], data[2][4]);
                        buttonRightY = Math.max(buttonRightY, data[2][1], data[2][3], data[2][5]);
                        lastX = data[2][4];
                        lastY = data[2][5];
                    } else {
                        leftX = Math.min(leftX, data[2][0]);
                        leftY = Math.min(leftY, data[2][1]);
                        buttonRightX = Math.max(buttonRightX, data[2][0]);
                        buttonRightY = Math.max(buttonRightY, data[2][1]);
                        lastX = data[2][0];
                        lastY = data[2][1];
                    }
                } else if (data[1] == "quadraticCurveTo") {
                    //
                    let mid1X = (lastX + data[2][0]) * 0.5;
                    let mid1Y = (lastY + data[2][1]) * 0.5;
                    let mid2X = (data[2][0] + data[2][2]) * 0.5;
                    let mid2Y = (data[2][1] + data[2][3]) * 0.5;
                    if (leftX == undefined) {
                        leftX = mid1X;
                    }
                    if (leftY == undefined) {
                        leftY = mid1Y;
                    }
                    if (buttonRightX == undefined) {
                        buttonRightX = mid1X;
                    }
                    if (buttonRightY == undefined) {
                        buttonRightY = mid1Y;
                    }
                    leftX = Math.min(leftX, mid1X, mid2X, data[2][2]);
                    leftY = Math.min(leftY, mid1Y, mid2Y, data[2][3]);
                    buttonRightX = Math.max(buttonRightX, mid1X, mid2X, data[2][2]);
                    buttonRightY = Math.max(buttonRightY, mid1Y, mid2Y, data[2][3]);
                    lastX = data[2][2];
                    lastY = data[2][3];
                } else if (data[1] == "arc") {
                    let yuanPointX = data[2][0];
                    let yuanPointY = data[2][1];
                    let radio = data[2][2];
                    let yuanLeftX = yuanPointX - radio;
                    let yuanLeftY = yuanPointY - radio;
                    let yuanBRX = yuanPointX + radio;
                    let yuanBRY = yuanPointY + radio;
                    if (leftX == undefined) {
                        leftX = yuanLeftX;
                    }
                    if (leftY == undefined) {
                        leftY = yuanLeftY;
                    }
                    if (buttonRightX == undefined) {
                        buttonRightX = yuanBRX;
                    }
                    if (buttonRightY == undefined) {
                        buttonRightY = yuanBRY;
                    }
                    leftX = Math.min(leftX, yuanLeftX);
                    leftY = Math.min(leftY, yuanLeftY);
                    buttonRightX = Math.max(buttonRightX, yuanBRX);
                    buttonRightY = Math.max(buttonRightY, yuanBRY);
                }
            } else {
                if (data[1] == "lineWidth") {
                    if (lineWidth < data[2]) {
                        lineWidth = data[2];
                    }
                }
            }
        }
        if (leftX != undefined || lineWidth > 0) {
            if (leftX == undefined) {
                leftX = 0;
                leftY = 0;
            }
            leftX -= 20 + lineWidth >> 1;
            leftY -= 20 + lineWidth >> 1;
            buttonRightX += 20 + lineWidth >> 1;
            buttonRightY += 20 + lineWidth >> 1;
            let w = buttonRightX - leftX;
            let h = buttonRightY - leftY;
            s.offsetX = leftX;
            s.offsetY = leftY;
            s._localBoundsSelf.x = leftX + 10;
            s._localBoundsSelf.y = leftY + 10;
            s._localBoundsSelf.width = w - 20;
            s._localBoundsSelf.height = h - 20;
            let _canvas: any = s.canvas;
            let ctx = s.context;
            _canvas.width = w;
            _canvas.height = h;
            ctx.clearRect(0, 0, w, h);
            ctx.setTransform(1, 0, 0, 1, -leftX, -leftY);
            s._drawShape(ctx);
            //
            s.anchorTexture.set(-leftX / w, -leftY / h) //= { x: -leftX / w, y: -leftY / h };
        } else {
            s.canvas.width = 0;
            s.canvas.height = 0;
            s.offsetX = 0;
            s.offsetY = 0;
            s._localBoundsSelf.clear();
            s.anchorTexture.set(0, 0) //= { x: 0, y: 0 };
        }
        //
        s.texture.update();
        //,_localBoundsSelf
        s._onTextureUpdate();//anchorTexturecanvas
    }

    /**
     * @method _drawShape
     * @param ctx
     * @private
     * @return {void}
     */
    private _drawShape(ctx: any): void {
        let s = this;
        let com = s._command;
        let cLen = com.length;
        let data: any;
        let leftX: number = s.offsetX;
        let leftY: number = s.offsetY;
        for (let i = 0; i < cLen; i++) {
            data = com[i];
            if (data[0] > 0) {
                let paramsLen = data[2].length;
                if (paramsLen == 0) {
                    ctx[data[1]]();
                } else if (paramsLen == 2) {
                    ctx[data[1]](data[2][0], data[2][1]);
                } else if (paramsLen == 4) {
                    ctx[data[1]](data[2][0], data[2][1], data[2][2], data[2][3]);
                } else if (paramsLen == 5) {
                    ctx[data[1]](data[2][0], data[2][1], data[2][2], data[2][3], data[2][4]);
                } else if (paramsLen == 6) {
                    let lx = data[2][4];
                    let ly = data[2][5];
                    if (data[0] == 2) {
                        //
                        lx -= leftX;
                        ly -= leftY;
                    }
                    ctx[data[1]](data[2][0], data[2][1], data[2][2], data[2][3], lx, ly);
                }
            } else {
                ctx[data[1]] = data[2];
            }
        }
    }

    /**
     * hitTestPoint
     * @method  hitTestPoint
     * @param {Point} globalPoint
     * @param {boolean} isMouseEvent
     * @return {any}
     * @public
     * @since 1.0.0
     */
    public hitTestPoint(globalPoint: Point, isMouseEvent: boolean = false): DisplayObject {
        let s: Shape = this;
        this.updateShape();//shape
        //
        var hitResult = super.hitTestPoint(globalPoint, isMouseEvent)
        //null
        if (!hitResult) return null;
        //
        if (hitResult != s) return hitResult;
        // 
        if (!s.hitTestByPixel) return s;
        let p: Point = globalPoint;
        //p
        if (isMouseEvent) p = s.globalToLocal(globalPoint);
        let image = s._texture;
        //null
        if (!image || image.width == 0 || image.height == 0) return null;
        // var backupCanvas = getBackupCanvas();
        // backupCanvas.width = 1;
        // backupCanvas.height = 1;
        p.x -= s.offsetX;
        p.y -= s.offsetY;
        let ctx = getBackupCanvasCtx() //backupCanvas.getContext('2d');
        ctx.setTransform(1, 0, 0, 1, 0, 0);//,
        ctx.clearRect(0, 0, 1, 1);
        ctx.setTransform(1, 0, 0, 1, -p.x, -p.y);
        ctx.drawImage(s.canvas, 0, 0);
        //imageData
        var imageData = ctx.getImageData(0, 0, 1, 1);
        //(bugimageDataundefined)
        if (!imageData || !imageData.data) return s;
        //0
        if (imageData.data[3] > 0) return s;
        return null
    }

    /**
     * ,.
     * @method changeColor
     * @param {Object} infoObj
     * @param {string|any} infoObj.fillColor "#fff"  "rgba(255,255,255,1)"Shape.getGradientColor();
     * @param {string} infoObj.strokeColor "#fff"  "rgba(255,255,255,1)";
     * @param {number} infoObj.lineWidth "1,2,3...";
     * @public
     * @since 1.0.2
     */
    public changeColor(infoObj: any): void {
        let s: Shape = this;
        let cLen: number = s._command.length;
        let c = s._command;
        for (let i = 0; i < cLen; i++) {
            if (c[i][0] == 0) {
                if (c[i][1] == "fillStyle" && infoObj.fillColor && c[i][2] != infoObj.fillColor) {
                    c[i][2] = infoObj.fillColor;
                    s.dirty = true;
                }
                if (c[i][1] == "strokeStyle" && infoObj.strokeColor && c[i][2] != infoObj.strokeColor) {
                    c[i][2] = infoObj.strokeColor;
                    s.dirty = true;
                }
                if (c[i][1] == "lineWidth" && infoObj.lineWidth && c[i][2] != infoObj.lineWidth) {
                    c[i][2] = infoObj.lineWidth;
                    s.dirty = true;
                }
            }
        }
    }
    /**
     * shape
     */
    // _calculateBounds() {
    //     this.updateShape();
    //     super._calculateBounds();
    // }
    //updateShape_boundId++calculateBounds_TODO
    calculateBounds() {
        this.updateShape();
        super.calculateBounds();
    }

    _renderCanvas(renderer) {
        this.updateShape();
        super._renderCanvas(renderer);
    }
    _renderWebGL(renderer) {
        this.updateShape();
        super._renderWebGL(renderer)
    }


    public destroy(): void {
        //
        let s = this;
        s._command = null;
        s._isBitmapStroke = null;
        s._isBitmapFill = null;
        super.destroy();
    }
}
