var EMPTY_ARRAY_BUFFER = new ArrayBuffer(0);

/**
 * webGL buffer
 * @class
 * @memberof glCore
 * @param gl {WebGLRenderingContext} The current WebGL rendering context
 * @param type {gl.ARRAY_BUFFER | gl.ELEMENT_ARRAY_BUFFER} @mat
 * @param data {ArrayBuffer| SharedArrayBuffer|ArrayBufferView} an array of data
 * @param drawType {gl.STATIC_DRAW|gl.DYNAMIC_DRAW|gl.STREAM_DRAW}
 */
export class GLBuffer {
	/**
	 * 
	 * @member {WebGLRenderingContext}
	 */
	gl: any;
	/**
	 * The WebGL buffer, created upon instantiation
	 * @member {WebGLBuffer}
	 */
	buffer: any;
	/**
	 * buffer
	 * 
	 * @member {gl.ARRAY_BUFFER|gl.ELEMENT_ARRAY_BUFFER}
	 */
	type: any;

	/**
	 * The draw type of the buffer
	 * 
	 * gl.STATIC_DRAW: 
	 * gl.DYNAMIC_DRAW: 
	 * gl.STREAM_DRAW: ,
	 * @member {gl.STATIC_DRAW|gl.DYNAMIC_DRAW|gl.STREAM_DRAW}
	 */
	drawType: any;

	/**
	 * The data in the buffer, as a typed array
	 * ArrayBuffer 
	 *  DataView 
	 * 
	 * @member {ArrayBuffer| SharedArrayBuffer|ArrayBufferView}
	 */
	data;

	/**
	 * ID
	 */
	_updateID: number;
	constructor(gl, type?, data?, drawType?) {

		this.gl = gl;

		this.buffer = gl.createBuffer();

		this.type = type || gl.ARRAY_BUFFER;

		this.drawType = drawType || gl.STATIC_DRAW;

		this.data = EMPTY_ARRAY_BUFFER;

		if (data) {
			this.upload(data);
		}

		this._updateID = 0;
	};

	/**
	 * 
	 * Uploads the buffer to the GPU
	 * @param data {ArrayBuffer| SharedArrayBuffer|ArrayBufferView} an array of data to upload
	 * @param offset {Number} if only a subset of the data should be uploaded, this is the amount of data to subtract
	 * @param dontBind {Boolean} whether to bind the buffer before uploading it  buffer
	 */
	public upload(data, offset?:number, dontBind?:boolean) {
		// todo - needed?
		if (!dontBind) this.bind();

		var gl = this.gl;

		data = data || this.data;
		offset = offset || 0;

		if (this.data.byteLength >= data.byteLength) {
			gl.bufferSubData(this.type, offset, data);
		}
		else {
			gl.bufferData(this.type, data, this.drawType);
		}

		this.data = data;
	};

	/**
	 * Binds the buffer
	 * buffer
	 * 
	 */
	public bind() {
		var gl = this.gl;
		gl.bindBuffer(this.type, this.buffer);
	};
	/**
	 * Destroys the buffer
	 *
	 */
	public destroy = function () {
		this.gl.deleteBuffer(this.buffer);
	};
	/**
	 * 
	 * @param gl 
	 * @param data 
	 * @param drawType 
	 */
	public static createVertexBuffer(gl, data?, drawType?) {
		return new GLBuffer(gl, gl.ARRAY_BUFFER, data, drawType);
	};

	/**
	 * 
	 * @param gl 
	 * @param data 
	 * @param drawType 
	 */
	public static createIndexBuffer(gl, data?, drawType?) {
		return new GLBuffer(gl, gl.ELEMENT_ARRAY_BUFFER, data, drawType);
	};

	public static create(gl, type, data, drawType) {
		return new GLBuffer(gl, type, data, drawType);
	};


}


