


import { EventDispatcher } from "../events/EventDispatcher";
import { createImage, getEnv } from "../utils";

/**
 * 
 * 
 * EventDispatcher
 * 
 * GlobalLoader
 */
export class Loader extends EventDispatcher {
    /**
     * jsonimage
     * RES
     */
    private caches = {};

    /**
     * 
     */
    constructor() {
        super();
        this._instanceType = "Loader";
    }

    /**
     * 
     * @param callback 
     * @param url png,json,callback
     */
    loadSheet(callback: (success: boolean, sheet?: { json: any, img: HTMLImageElement }) => void, url: string) {
        let pngFile = url.substring(0, url.lastIndexOf('.')) + '.png';
        this.loadImage((suc, data) => {
            if (suc) {
                this.cache(pngFile, data);
                if (this.caches[url]) {
                    callback(true, { json: this.caches[url], img: data })
                    //,
                    this.caches[url] = null;
                    this.caches[pngFile] = null;
                }
            } else {
                callback(false)
            }
        }, pngFile);


        this.loadJson((suc, data) => {
            if (suc) {
                this.cache(url, data);
                if (this.caches[pngFile]) {
                    callback(true, { json: data, img: this.caches[pngFile] });
                    //,
                    this.caches[url] = null;
                    this.caches[pngFile] = null;
                }
            } else {
                callback(false)
            }
        }, url)

        //fetchsafari
        // fetchAsync(url)
        //     .then((data) => {
        //         this.cache(url, data);
        //         if (this.caches[pngFile]) {
        //             callback(true, { json: data, img: this.caches[pngFile] })
        //         }
        //         // console.log(data)
        //     })
        //     .catch(reason => {
        //         callback(false, reason)
        //         // console.log(reason.message)
        //     })
    }
    /**
     * json
     * @param callback 
     * @param url 
     */
    loadJson(callback: (s: boolean, res: any) => void, url: string) {
        //@ts-ignore ,requestdownloadFile
        // my.request({
        //     url: url,
        //     dataType: "json",
        //     success: (res) => {
        //         callback(true, res.data)
        //     },
        //     fail: function (res) {
        //         // my.alert({
        //         //   title: JSON.stringify(res)
        //         // });
        //         callback(false, res)
        //     }
        // });
        if (getEnv() == "tb") {
            // this.tbLoad(callback, url, "utf8")
            this.tbLoad((s, res) => {
                callback(s, s ? JSON.parse(res) : res)//utf8
            }, url, "utf8")
            return
        }
        this.loadRawWeb(callback, url, "json")
    }
    /**
     * ArrayBuffer
     * @param callback 
     * @param url 
     */
    loadAB(callback: (s: boolean, res: ArrayBuffer) => void, url: string) {
        if (getEnv() == "tb") {
            GlobalLoader.tbLoad(callback, url, "ArrayBuffer")
        } else {
            GlobalLoader.loadRawWeb(callback, url, "arraybuffer")
        }
    }
    /**
     * web
     * @param callback 
     * @param url 
     * @param type 
     */
    loadRawWeb(
        callback: (s: boolean, res: any) => void,
        url: string,
        type: 'text' | 'json' | 'arraybuffer' = "json"
    ) {
        //new
        let _req;
        if (window["XMLHttpRequest"]) {
            _req = new XMLHttpRequest();
        } else if (window["ActiveXObject"]) {
            _req = new window["ActiveXObject"]();
        } else {
            alert("")
        }
        if (_req != null) {
            _req.open("GET", url, true);
            _req.responseType = type;
            _req.send();
            _req.onreadystatechange = () => {
                if (_req.readyState == 4 && _req.status == 200) {
                    callback(true, _req.response)
                }
            };
            _req.onerror = (reason): void => {
                callback(false, reason)
            }
        }
    }
    /**
     * 
     * @param callback 
     * @param url 
     */
    loadImage(callback: (s: boolean, image?: HTMLImageElement) => void, url: string) {
        let img = createImage();
        //
        // window["Image"] = function () {
        //     var image = canvas.createImage()
        //     return image
        // }
        // let img = new Image()
        img.onload = function () {
            callback(true, img);
        }
        img.onerror = function () {
            callback(false);
        }
        //
        url = encodeURI(url)
        //base64anonymous
        if (url.indexOf('data:') !== 0) img.crossOrigin = "anonymous";
        img.src = url;
    }
    /**
     * 
     * @param callback 
     * @param url 
     */
    loadAudio(callback: (s: boolean, audio?) => void, url) {
        //@ts-ignore
        const innerAudioContext = my.createInnerAudioContext()
        // innerAudioContext.autoplay = true
        innerAudioContext.src = url;
        innerAudioContext.onCanplay(() => {
            callback(true, innerAudioContext)
        })
        // innerAudioContext.onPlay(() => {
        //     console.log('')
        // })
        innerAudioContext.onError((res) => {
            // console.log(res.errMsg)
            callback(false)
        })
        // innerAudioContext.onEnded(() => {
        //     console.log('')
        // })
    }

    /**
     * 
     * @param callback 
     * @param url cloud
     */
    tbLoad(
        callback: (s: boolean, res?: any) => void,
        url: string,
        type: "utf8" | "ArrayBuffer" = "ArrayBuffer"
    ) {
        //"cloud://A8673B47AAA58993A24A6718E203B967//dice.svga"
        if (url.indexOf("cloud://") == 0) {
            this.getTbTempUrl((src) => {
                this.downloadReadFile(callback, src, type)
            }, url)
        } else {
            this.downloadReadFile(callback, url, type)
        }
    }

    /**
     * cdn
     * @param callback 
     * @param url 
     */
    private getTbTempUrl(callback: (src: string) => void, url) {
        //@ts-ignore
        const { cloud } = getApp();
        //
        cloud.file.getTempFileURL({ fileId: [url] }).then((urls) => {
            callback(urls[0].url.replace('-internal', ''))
        }).catch((err) => {
            console.error(err)
        })
    }

    /**
     * 
     * @param callback 
     * @param url 
     * @param type  ArrayBuffer 
     */
    private downloadReadFile(
        callback: (s: boolean, res?: any) => void,
        url: string,
        type: "utf8" | "ArrayBuffer" = "ArrayBuffer"
    ) {
        //@ts-ignore
        let tbMy = my;
        tbMy.downloadFile({
            url: url,
            success(res) {
                var i = res.apFilePath;//
                tbMy.getFileSystemManager().readFile({
                    filePath: i,
                    encoding: type === "ArrayBuffer" ? "" : type,//ArrayBuffer
                    success: function (r) {
                        callback(true, r.data)//r.data
                        // actions.load_viaProto(r.data, cb, failure);
                    },
                    fail: function (res) {
                        callback(false, res)
                    }
                })
            },
            fail(res) {
                callback(false, res)
            },
        });
    }

    private cache(name: string, data: any) {
        if (this.caches[name]) {
            console.log("" + name);
        }
        this.caches[name] = data
    }


}

export const GlobalLoader = new Loader();
