import { HashObject } from "../HashObject";
import { Point } from "./Point";
import { SHAPES } from "../const";
import { Matrix } from "./Matrix";
/**
 *
 * @class Rectangle
 * @extends HashObject
 * @public
 * @since 1.0.0
 */
export class Rectangle extends HashObject {

    /**
     * 
     */
    type: number
    /**
     * 
     * @method Rectangle
     * @param {number} x
     * @param {number} y
     * @param {number} width
     * @param {number} height
     */
    public constructor(x: number = 0, y: number = 0, width: number = 0, height: number = 0) {
        super();
        var s = this;
        s._instanceType = "Rectangle";
        s.x = x;
        s.y = y;
        s.height = height;
        s.width = width;

        this.type = SHAPES.RECT
    }

    clear() {
        this.x = 0;
        this.y = 0;
        this.width = 0;
        this.height = 0;
    }

    copy(rect: Rectangle) {
        this.x = rect.x;
        this.y = rect.y;
        this.width = rect.width;
        this.height = rect.height;

        return this;
    }

    clone() {
        return new Rectangle(this.x, this.y, this.width, this.height);
    }

    /**
     *  x 
     * @property x
     * @public
     * @since 1.0.0
     * @type{number}
     * @default 0
     */
    public x: number = 0;
    /**
     *  y 
     * @property y
     * @public
     * @since 1.0.0
     * @type{number}
     * @default 0
     */
    public y: number = 0;
    /**
     * 
     * @property width
     * @public
     * @since 1.0.0
     * @type{number}
     * @default 0
     */
    public width: number = 0;
    /**
     * 
     * @property height
     * @public
     * @since 1.0.0
     * @type{number}
     * @default 0
     */
    public height: number = 0;

    /**
     * returns the left edge of the rectangle
     */
    get left(): number {
        return this.x;
    }

    /**
     * returns the right edge of the rectangle
     */
    get right(): number {
        return this.x + this.width;
    }

    /**
     * returns the top edge of the rectangle
     */
    get top(): number {
        return this.y;
    }

    /**
     * returns the bottom edge of the rectangle
     */
    get bottom(): number {
        return this.y + this.height;
    }

    /**
     * 
     * @method isPointIn
     * @param {Point} point
     * @return {boolean}
     * @public
     * @since 1.0.0
     */
    public isPointIn(point: Point): boolean {
        let s = this;
        return point.x >= s.x && point.x <= (s.x + s.width) && point.y >= s.y && point.y <= (s.y + s.height);
    }

    /**
     * Fits this rectangle around the passed one.
     *
     * @param {Rectangle} rectangle - The rectangle to fit.
     */
    fit(rectangle: Rectangle) {
        var x1 = Math.max(this.x, rectangle.x);
        var x2 = Math.min(this.x + this.width, rectangle.x + rectangle.width);
        var y1 = Math.max(this.y, rectangle.y);
        var y2 = Math.min(this.y + this.height, rectangle.y + rectangle.height);

        this.x = x1;
        this.width = Math.max(x2 - x1, 0);
        this.y = y1;
        this.height = Math.max(y2 - y1, 0);
    };
    /**
     * Pads the rectangle making it grow in all directions.
     *
     * @param {number} paddingX - The horizontal padding amount.
     * @param {number} [paddingY] - The vertical padding amount.
     */
    pad(paddingX: number, paddingY?: number) {
        paddingX = paddingX || 0;
        paddingY = paddingY || ((paddingY !== 0) ? paddingX : 0);

        this.x -= paddingX;
        this.y -= paddingY;

        this.width += paddingX * 2;
        this.height += paddingY * 2;
    }

    /**
     * ,
     * @method createFromRects
     * @param {Rectangle} rect
     * @param {..arg} arg
     * @public
     * @since 1.0.0
     * @static
     */
    public static createFromRects(...arg: Rectangle[]): Rectangle {
        if (arg.length == 0) {
            return null;
        } else if (arg.length == 1) {
            return arg[0];
        } else {
            let rect = arg[0];
            let x = rect.x, y = rect.y, w = rect.width, h = rect.height, wx1: number, wx2: number, hy1: number, hy2: number;
            for (let i: number = 1; i < arg.length; i++) {
                //xy;
                if (!arg[i].width && !arg[i].height) continue;
                wx1 = x + w;
                hy1 = y + h;
                wx2 = arg[i].x + arg[i].width;
                hy2 = arg[i].y + arg[i].height;
                if (x > arg[i].x /*|| wx1 == 0*/ || (x == 0 && w == 0)) {//wx1hy1==0,xw0
                    x = arg[i].x;
                }
                if (y > arg[i].y /*|| hy1 == 0*/ || (y == 0 && h == 0)) {// TODO
                    y = arg[i].y;
                }
                if (wx1 < wx2) {
                    wx1 = wx2;
                }
                if (hy1 < hy2) {
                    hy1 = hy2;
                }
                rect.x = x;
                rect.y = y;
                rect.width = wx1 - x;
                rect.height = hy1 - y;
            }
            return rect;
        }
    }
    /**
     * 
     * 
     * @method createFromPoints
     * @static
     * @public
     * @since 1.0.0
     * @param {Point} p1
     * @param {..arg} ary
     * @return {Rectangle}
     */
    public static createFromPoints(rect: Rectangle, ...arg: Point[]): Rectangle {
        let x = arg[0].x, y = arg[0].y, w = arg[0].x, h = arg[0].y;
        for (let i: number = 1; i < arg.length; i++) {
            if (arg[i] == null) continue;
            if (x > arg[i].x) {
                x = arg[i].x;
            }
            if (y > arg[i].y) {
                y = arg[i].y;
            }
            if (w < arg[i].x) {
                w = arg[i].x;
            }
            if (h < arg[i].y) {
                h = arg[i].y;
            }
        }
        rect.x = x;
        rect.y = y;
        rect.width = w - x;
        rect.height = h - y;
        return rect;
    }

    /**
     *   [0,1,
     *               2,3,
     *               1,3,
     *               1,0]
     * @param rect 
     * @param vertexData 8
     */
    public static createFromVertexData(rect: Rectangle, vertexData: Float32Array, matrix?: Matrix): Rectangle {
        let temP: Point = new Point()
        let x = vertexData[0], y = vertexData[1], w = vertexData[0], h = vertexData[1];
        if (matrix) {
            matrix.transformPoint(x, y, temP);
            x = w = temP.x;
            y = h = temP.y;
        }
        for (let i: number = 2; i < vertexData.length; i += 2) {
            if (vertexData[i] == null) continue;
            temP.set(vertexData[i], vertexData[i + 1]);
            if (matrix) matrix.transformPoint(temP.x, temP.y, temP);
            if (x > temP.x) x = temP.x;
            if (y > temP.y) y = temP.y;
            if (w < temP.x) w = temP.x;
            if (h < temP.y) h = temP.y;
        }
        rect.x = x;
        rect.y = y;
        rect.width = w - x;
        rect.height = h - y;
        return rect;
    }

    /**
     * 
     * @method createRectform2Point
     * @static
     * @param rect
     * @param p1
     * @param p2
     * @return 
     */
    public static createRectfrom2Point(rect: Rectangle, p1: Point, p2: Point): Rectangle {
        let x = p1.x, y = p1.y, w = p1.x, h = p1.y;
        if (x > p2.x) {
            x = p2.x;
        }
        if (y > p2.y) {
            y = p2.y;
        }
        if (w < p2.x) {
            w = p2.x;
        }
        if (h < p2.y) {
            h = p2.y;
        }
        rect.x = x, rect.y = y, rect.width = w - x, rect.height = h - y;
        return rect;
    }
    /**
     * 
     * @method testRectCross
     * @public
     * @since 1.0.2
     * @param r1
     * @param r2
     * @return {boolean}
     */
    public static testRectCross(ra: Rectangle, rb: Rectangle): boolean {
        let a_cx: number, a_cy: number; /* */
        let b_cx: number, b_cy: number; /* */
        a_cx = ra.x + (ra.width / 2);
        a_cy = ra.y + (ra.height / 2);
        b_cx = rb.x + (rb.width / 2);
        b_cy = rb.y + (rb.height / 2);
        return ((Math.abs(a_cx - b_cx) <= (ra.width / 2 + rb.width / 2)) && (Math.abs(a_cy - b_cy) <= (ra.height / 2 + rb.height / 2)));
    }

    public destroy(): void {
    }
}
