
/**
 * Helper class to create a WebGL Texture
 * WebGL Texture
 * @class
 * @memberof glCore
 * @param gl {WebGLRenderingContext} The current WebGL context
 * @param width {number} the width of the texture
 * @param height {number} the height of the texture
 * @param format {number} the pixel format of the texture. defaults to gl.RGBA
 * @param type {number} the gl type of the texture. defaults to gl.UNSIGNED_BYTE
 */
export class GLTexture {
	/**
	 * 
	 * The current WebGL rendering context
	 */
	gl: WebGLRenderingContext;
	texture: WebGLTexture;
	/**
	 * If mipmapping was used for this texture, enable and disable with enableMipmap()
	 * 1024*1024512*512256*256128*1281*1;
	 */
	mipmap: boolean;
	/**
	 * Set to true to enable pre-multiplied alpha
	 * truehttps://blog.csdn.net/mydreamremindme/article/details/50817294
	 */
	premultiplyAlpha;
	/**
	 * 
	 */
	width: number;
	/**
	 * 
	 */
	height: number;
	/**
	 * {number} the pixel format of the texture. defaults to gl.RGBA
	 * gl.RGBA  gl.RGB
	 */
	format: any;
	/**
	 * {number} the gl type of the texture. defaults to gl.UNSIGNED_BYTE
	 * gl.UNSIGNED_BYTE   //https://developer.mozilla.org/zh-CN/docs/Web/API/WebGLRenderingContext/texImage2D
	 */
	type: any;
	constructor(gl: WebGLRenderingContext, width?: number, height?: number, format?, type?) {

		this.gl = gl;

		this.texture = gl.createTexture();

		// some settings..
		this.mipmap = false;

		this.premultiplyAlpha = false;

		this.width = width || -1;

		this.height = height || -1;

		/**
		 * The pixel format of the texture. defaults to gl.RGBA
		 *
		 * @member {Number}
		 */
		this.format = format || gl.RGBA;

		/**
		 * The gl type of the texture. defaults to gl.UNSIGNED_BYTE
		 *
		 * @member {Number}
		 */
		this.type = type || gl.UNSIGNED_BYTE;
	};


	/**
	 * Uploads this texture to the GPU
	 * GPU
	 * @param source {HTMLImageElement|ImageData|HTMLVideoElement} the source image of the texture
	 */
	public upload(source) {

		this.bind();

		var gl = this.gl;

		//
		gl.pixelStorei(gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL, this.premultiplyAlpha);

		var newWidth = source.videoWidth || source.width;
		var newHeight = source.videoHeight || source.height;

		if (newHeight !== this.height || newWidth !== this.width) {
			//https://developer.mozilla.org/zh-CN/docs/Web/API/WebGLRenderingContext/texImage2D
			gl.texImage2D(gl.TEXTURE_2D, 0, this.format, this.format, this.type, source);
		}
		else {
			//https://developer.mozilla.org/en-US/docs/Web/API/WebGLRenderingContext/texSubImage2D
			gl.texSubImage2D(gl.TEXTURE_2D, 0, 0, 0, this.format, this.type, source);
		}

		// if the source is a video, we need to use the videoWidth / videoHeight properties as width / height will be incorrect.
		this.width = newWidth;
		this.height = newHeight;
	};

	/**
	 * Use a data source and uploads this texture to the GPU
	 * 
	 * @param data {TypedArray} the data to upload to the texture
	 * @param width {number} the new width of the texture
	 * @param height {number} the new height of the texture
	 */
	public uploadData = function (data, width, height) {
		this.bind();

		var gl = this.gl;


		if (data instanceof Float32Array) {
			if (!FLOATING_POINT_AVAILABLE) {
				var ext = gl.getExtension("OES_texture_float");

				if (ext) {
					FLOATING_POINT_AVAILABLE = true;
				}
				else {
					throw new Error('floating point textures not available');
				}
			}

			this.type = gl.FLOAT;
		}
		else {
			// TODO support for other types
			this.type = this.type || gl.UNSIGNED_BYTE;
		}

		// what type of data?
		gl.pixelStorei(gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL, this.premultiplyAlpha);


		if (width !== this.width || height !== this.height) {
			gl.texImage2D(gl.TEXTURE_2D, 0, this.format, width, height, 0, this.format, this.type, data || null);
		}
		else {
			gl.texSubImage2D(gl.TEXTURE_2D, 0, 0, 0, width, height, this.format, this.type, data || null);
		}

		this.width = width;
		this.height = height;


		//	texSubImage2D
	};

	/**
	 * Binds the texture
	 * location
	 * @param  location
	 */
	public bind(location?: number) {
		var gl = this.gl;

		if (location !== undefined) {
			gl.activeTexture(gl.TEXTURE0 + location);
		}

		gl.bindTexture(gl.TEXTURE_2D, this.texture);
	};

	/**
	 * Unbinds the texture
	 * 
	 */
	public unbind() {
		var gl = this.gl;
		gl.bindTexture(gl.TEXTURE_2D, null);
	};

	/**
	 * @param linear {Boolean} if we want to use linear filtering or nearest neighbour interpolation
	 *  
	 */
	public minFilter(linear: boolean) {
		var gl = this.gl;

		this.bind();

		if (this.mipmap) {
			gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, linear ? gl.LINEAR_MIPMAP_LINEAR : gl.NEAREST_MIPMAP_NEAREST);
		}
		else {
			gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, linear ? gl.LINEAR : gl.NEAREST);
		}
	};

	/**
	 * @param linear {Boolean} if we want to use linear filtering or nearest neighbour interpolation
	 *  
	 */
	public magFilter(linear: boolean) {
		var gl = this.gl;

		this.bind();

		gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, linear ? gl.LINEAR : gl.NEAREST);
	};

	/**
	 * Enables mipmapping
	 * 2
	 */
	public enableMipmap() {
		var gl = this.gl;

		this.bind();

		this.mipmap = true;

		gl.generateMipmap(gl.TEXTURE_2D);
	};

	/**
	 * Enables linear filtering
	 * 
	 */
	public enableLinearScaling() {
		this.minFilter(true);
		this.magFilter(true);
	};

	/**
	 * Enables nearest neighbour interpolation
	 * 
	 */
	public enableNearestScaling() {
		this.minFilter(false);
		this.magFilter(false);
	};

	/**
	 * Enables clamping on the texture so WebGL will not repeat it
	 * 2
	 */
	public enableWrapClamp() {
		var gl = this.gl;

		this.bind();

		gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE);
		gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE);
	};

	/**
	 * Enable tiling on the texture
	 * 
	 */
	public enableWrapRepeat() {
		var gl = this.gl;

		this.bind();

		gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.REPEAT);
		gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.REPEAT);
	};
	/**
	 * 
	 */
	public enableWrapMirrorRepeat() {
		var gl = this.gl;

		this.bind();

		gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.MIRRORED_REPEAT);
		gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.MIRRORED_REPEAT);
	};


	/**
	 * Destroys this texture
	 */
	public destroy() {
		var gl = this.gl;
		//TODO
		gl.deleteTexture(this.texture);
	};

	/**
	 * 
     * @static
     * @param gl {WebGLRenderingContext} The current WebGL context
     * @param source {HTMLImageElement|ImageData} the source image of the texture
     * @param premultiplyAlpha {Boolean} If we want to use pre-multiplied alpha
     */
	public static fromSource(gl: WebGLRenderingContext, source: HTMLImageElement | ImageData, premultiplyAlpha?: boolean) {
		var texture: GLTexture = new GLTexture(gl);
		texture.premultiplyAlpha = premultiplyAlpha || false;
		texture.upload(source);
		return texture;
	};

	/**
	 * @static
	 * @param gl {WebGLRenderingContext} The current WebGL context
	 * @param data {TypedArray} the data to upload to the texture
	 * @param width {number} the new width of the texture
	 * @param height {number} the new height of the texture
	 */
	public static fromData(gl, data, width, height) {
		//console.log(data, width, height);
		var texture = new GLTexture(gl);
		texture.uploadData(data, width, height);

		return texture;
	};

}



var FLOATING_POINT_AVAILABLE = false;







