import { osType, TEXT_lINETYPE } from "../const";
import { Event } from "../events/Event";
import { MouseEvent } from "../events/MouseEvent";
import { FloatDisplay } from "../display/FloatDisplay";
import { TextField } from "./TextField";

/**
 * dom,
 * ,,
 */
export class InputText extends FloatDisplay {
    /**
     * .
     * @property inputType
     * @public
     * @since 1.0.0
     * @type {number} 0 input 1 password 2 mulit
     * @default 0
     */
    public inputType: number = 0;

    /**
     * pc
     * @property isAutoDownKeyBoard
     * @type {boolean}
     * @since 1.0.3
     * @default true
     */
    public isAutoDownKeyBoard: boolean = true;
    /**
     * @property _inputTypeList
     * @static
     * @type {string[]}
     * @private
     * @since 2.0.0
     */
    private static _inputTypeList: Array<string> = ["input", "password", "textarea"];
    /**
     * @method InputText
     * @public
     * @since 1.0.0
     * @param {number} inputType 0 input 1 password 2 multiline
     * @example
     *      var inputText=new InputText();
     *      inputText.initInfo('aa','#ffffff','left',14,'',100,20);
     */
    public constructor(inputType: number = 0) {
        super();
        var input: any = null;
        let s: InputText = this;
        s._instanceType = "InputText";
        if (inputType < 2) {
            input = document.createElement("input");
            input.type = InputText._inputTypeList[inputType];
        } else {
            input = document.createElement("textarea");
            input.style.resize = "none";
            input.style.overflow = "hidden";
        }
        s.inputType = inputType;
        var remove = function () {
            if (s.isAutoDownKeyBoard && osType != "pc") {
                s.htmlElement && s.htmlElement.blur();
                window.scrollTo(0, 0)
            }
        }.bind(s);
        s.addEventListener(Event.REMOVED_FROM_STAGE, function (e: Event) {
            s.stage.removeEventListener(MouseEvent.MOUSE_UP, remove);
        });
        s.addEventListener(Event.ADDED_TO_STAGE, function (e: Event) {
            //stage
            s.stage.addEventListener(MouseEvent.MOUSE_UP, remove);

        });
        s.init(input);
    }

    /**
     * 
     * @method init
     * @param htmlElement
     * @public
     * @return {void}
     * @since 1.0.0
     */
    public init(htmlElement: any): void {
        super.init(htmlElement);
        //
        let s = this;
        s.htmlElement.style.outline = "none";
        s.htmlElement.style.borderWidth = "thin";
        s.htmlElement.style.borderColor = "#000";
    }
    /**
     * 
     * @method initInfo
     * @public
     * @since 1.0.0
     * @param {string} text 
     * @param {string}color 
     * @param {string}align 
     * @param {number}size  
     * @param {string}font  
     * @param {number}textWidth 
     * @param {number}textHeight 
     * @param {number}lineHeight ,
     * @param {boolean}showBorder 
     */
    public initInfo(
        text: string,
        color: string,
        align: string,
        size: number,
        font: string,
        textWidth: number,
        textHeight: number,
        lineHeight?: number,
        showBorder: boolean = false,
    ): void {
        let s: InputText = this;
        s.htmlElement.placeholder = text;
        //font
        s.htmlElement.style.font = size + "px " + (font || "");
        s.htmlElement.style.color = color;
        s.htmlElement.style.textAlign = align;
        ///////////////////////////////////
        s.border = showBorder;
        //color:blue; text-align:center"
        s.textWidth = textWidth;
        s.textHeight = textHeight;
        if (s.inputType == 2) {
            s.htmlElement.style.lineHeight = lineHeight + "px";
        }
    }

    /**
     * @property lineSpacing
     * @public
     * @since 2.0.0
     * @param {number} value
     */
    public set lineSpacing(value: number) {
        this.htmlElement.style.lineHeight = value + "px";
    }
    public get lineSpacing(): number {
        return parseInt(this.htmlElement.style.lineHeight);
    }
    /**
     * 
     * @property bold
     * @param {boolean} bold truefalse
     * @public
     * @since 1.0.3
     */
    public set bold(bold: boolean) {
        let ss = this.htmlElement.style;
        if (bold) {
            ss.fontWeight = "bold";
        } else {
            ss.fontWeight = "normal";
        }
    }

    public get bold(): boolean {
        return this.htmlElement.style.fontWeight == "bold";
    }

    /**
     * 
     * @property italic
     * @param {boolean} italic truefalse
     * @public
     * @since 1.0.3
     */
    public set italic(italic: boolean) {
        let s = this.htmlElement.style;
        if (italic) {
            s.fontStyle = "italic";
        } else {
            s.fontStyle = "normal";
        }
    }

    public get italic(): boolean {
        return this.htmlElement.style.fontStyle == "italic"
    }
    /**
     * 
     * @property textHeight
     * @public
     * @since 1.0.0
     * @type {number}
     * @default 0
     */
    public set textHeight(value: number) {
        this.htmlElement.style.height = value + "px";
    }

    public get textHeight(): number {
        return parseInt(this.htmlElement.style.height);
    }
    /**
     * 
     * @property textWidth
     * @public
     * @since 1.0.0
     * @type {number}
     * @default 0
     */
    public set textWidth(value: number) {
        this.htmlElement.style.width = value + "px";
    }

    public get textWidth(): number {
        return parseInt(this.htmlElement.style.width);
    }
    /**
     * 
     * @property color
     * @param {boolean} italic truefalse
     * @public
     * @since 1.0.3
     */
    public set color(value: string) {
        var ss = this.htmlElement.style;
        ss.color = value;
    }

    public get color(): string {
        return this.htmlElement.style.color;
    }

    /**
     * 
     * @property property
     * @param {boolean} show truefalse
     * @public
     * @since 1.0.3
     */
    public set border(show: boolean) {
        let s = this;
        if (show) {
            s.htmlElement.style.borderStyle = "inset";
            s.htmlElement.style.backgroundColor = "#fff";
        } else {
            s.htmlElement.style.borderStyle = "none";
            s.htmlElement.style.backgroundColor = "transparent";
        }
    }

    public get border(): boolean {
        return this.htmlElement.style.borderStyle != "none";
    }

    /**
     * 
     * getText setText 
     * @property text
     * @public
     * @since 1.0.3
     * @return {string}
     */
    public get text(): string {
        let s = this;
        if (s.htmlElement) {
            return s.htmlElement.value;
        }
    }

    public set text(value: string) {
        let s = this;
        if (s.htmlElement) {
            s.htmlElement.value = value;
        }
    }

    /**
     * 
     * @public
     * @since 1.1.0
     * @property maxCharacters
     * @return {number}
     */
    public get maxCharacters(): number {
        let l: any = this.htmlElement.getAttribute("maxlength");
        if (l === null) {
            return 0;
        } else {
            return l;
        }
    }
    public set maxCharacters(value: number) {
        this.htmlElement.setAttribute("maxlength", value);
    }

    /**
     * 
     * @param textField 
     */
    public convertToTextField(textField?: TextField): TextField {
        if (!this.htmlElement || !this.text) return null;
        textField = textField || new TextField();
        textField.fillColor = this.color;
        textField.text = this.text;
        textField.size = parseInt(this.htmlElement.style.font.split("px")[0]);
        textField.textAlign = this.htmlElement.style.textAlign;
        textField.bold = this.bold;
        textField.italic = this.italic;
        textField.textWidth = this.textWidth;
        textField.lineType = this.inputType == 2 ? TEXT_lINETYPE.MULTI : TEXT_lINETYPE.SINGLE;
        textField.position.copy(this.position);
        textField.scale.copy(this.scale);
        textField.rotation = this.rotation;
        return textField
    }
}
