
// state object//
import { setVertexAttribArrays } from './setVertexAttribArrays';

/**
 * Helper class to work with WebGL VertexArrayObjects (vaos)
 * Only works if WebGL extensions are enabled (they usually are)
 * VAOs
 * @class
 * @memberof glCore
 * @param gl {WebGLRenderingContext} The current WebGL rendering context
 */
export class VertexArrayObject {
    nativeVaoExtension: any;
    nativeState: any;
    nativeVao: any;
    /**
     * 
     */
    gl: WebGLRenderingContext;
    /**
     * An array of attributes
     * attributes
     */
    attributes: any[];

    /**
     * buffer
     *  @member {GLBuffer}
     */
    indexBuffer: any;
    /**
     * A boolean flag
     */
    dirty: boolean;
    constructor(gl: WebGLRenderingContext, state) {
        this.nativeVaoExtension = null;

        //
        if (!VertexArrayObject.FORCE_NATIVE) {
            this.nativeVaoExtension = gl.getExtension('OES_vertex_array_object') ||
                gl.getExtension('MOZ_OES_vertex_array_object') ||
                gl.getExtension('WEBKIT_OES_vertex_array_object');
        }

        this.nativeState = state;

        if (this.nativeVaoExtension) {
            this.nativeVao = this.nativeVaoExtension.createVertexArrayOES();

            var maxAttribs = gl.getParameter(gl.MAX_VERTEX_ATTRIBS);

            // VAO - overwrite the state..
            this.nativeState = {
                tempAttribState: new Array(maxAttribs),
                attribState: new Array(maxAttribs)
            };
        }

        this.gl = gl;

        this.attributes = [];

        this.indexBuffer = null;

        this.dirty = false;
    };

    /**
     * Binds the buffer
     * 
     */
    public bind() {
        if (this.nativeVao) {
            this.nativeVaoExtension.bindVertexArrayOES(this.nativeVao);

            if (this.dirty) {
                this.dirty = false;
                this.activate();
                return this;
            }
            if (this.indexBuffer) {
                this.indexBuffer.bind();
            }
        }
        else {
            this.activate();
        }

        return this;
    };

    /**
     * Unbinds the buffer
     * 
     */
    public unbind() {
        if (this.nativeVao) {
            this.nativeVaoExtension.bindVertexArrayOES(null);
        }

        return this;
    };

    /**
     * Uses this vao
     * vao
     */
    public activate() {

        var gl = this.gl;
        var lastBuffer = null;

        for (var i = 0; i < this.attributes.length; i++) {
            var attrib = this.attributes[i];

            if (lastBuffer !== attrib.buffer) {
                attrib.buffer.bind();
                lastBuffer = attrib.buffer;
            }

            // gl.vertexAttribPointer(attrib.attribute.location,
            //     attrib.attribute.size,
            //     attrib.type || gl.FLOAT,
            //     attrib.normalized || false,
            //     attrib.stride || 0,
            //     attrib.start || 0);
            attrib.attribute.pointer(
                attrib.type || gl.FLOAT,
                attrib.normalized || false,
                attrib.stride || 0,
                attrib.start || 0
            )
        }

        setVertexAttribArrays(gl, this.attributes, this.nativeState);

        if (this.indexBuffer) {
            this.indexBuffer.bind();
        }

        return this;
    };

    /**
     * attribute
     * @param buffer     {gl.GLBuffer}
     * @param attribute  {*}
     * @param type       {String}
     * @param normalized {Boolean}
     * @param stride     {Number}
     * @param start      {Number}
     * @param name       {string} 
     */
    public addAttribute(buffer, attribute, type?, normalized?, stride?, start?, name?: string) {
        this.attributes.push({
            buffer: buffer,
            attribute: attribute,

            location: attribute.location,
            type: type || this.gl.FLOAT,
            normalized: normalized || false,
            stride: stride || 0,
            start: start || 0,

            name: name,//
        });

        this.dirty = true;

        return this;
    };
    /**
     * attributeattributesTODO
     * @param name 
     * @param onlyOne  
     */
    public removeAttribute(name: string, onlyOne: boolean = true): this {
        let len = this.attributes.length;
        for (var i = len - 1; i >= 0; i--) {//
            let attr = this.attributes[i]
            if (attr.name === name) {//
                this.attributes.splice(i, 1);//
                //
                this.dirty = true;
                //break
                if (onlyOne) break;
            }
        }
        return this
    }

    /**
     * 
     * @param buffer   {gl.GLBuffer}
     */
    public addIndex(buffer/*, options*/) {
        this.indexBuffer = buffer;

        this.dirty = true;

        return this;
    };

    /**
     * Unbinds this vao and disables it
     * vao
     */
    public clear() {
        // var gl = this.gl;

        // TODO - should this function unbind after clear?
        // for now, no but lets see what happens in the real world!
        if (this.nativeVao) {
            this.nativeVaoExtension.bindVertexArrayOES(this.nativeVao);
        }

        this.attributes.length = 0;
        this.indexBuffer = null;

        return this;
    };

    /**
     * 
     * @param type  {Number} gl.TRIANGLES\gl.TRIANGLE_STRIP
     * @param size  {Number} 
     * @param start {Number} 
     */
    public draw(type, size?, start?) {
        var gl = this.gl;

        if (this.indexBuffer) {
            // https://developer.mozilla.org/en-US/docs/Web/API/WebGLRenderingContext/drawElements
            gl.drawElements(type, size || this.indexBuffer.data.length, gl.UNSIGNED_SHORT, (start || 0) * 2);
        }
        else {
            // https://developer.mozilla.org/en-US/docs/Web/API/WebGLRenderingContext/drawArrays
            // TODO need a better way to calculate size..
            gl.drawArrays(type, start || 0, size || this.getSize());
        }

        return this;
    };

    /**
     * Destroy this vao
     */
    public destroy() {
        // lose references
        this.gl = null;
        this.indexBuffer = null;
        this.attributes = null;
        this.nativeState = null;

        if (this.nativeVao) {
            this.nativeVaoExtension.deleteVertexArrayOES(this.nativeVao);
        }

        this.nativeVaoExtension = null;
        this.nativeVao = null;
    };

    public getSize() {
        var attrib = this.attributes[0];
        // return attrib.buffer.data.length / ((attrib.stride / 4) || attrib.attribute.size);
        //ArrayBufferlength0
        return (attrib.buffer.data.length || attrib.buffer.data.byteLength / 4) / ((attrib.stride / 4) || attrib.attribute.size);
    };

    /**
    * Some devices behave a bit funny when using the newer extensions (im looking at you ipad 2!)
    * If you find on older devices that things have gone a bit weird then set this to true.
    */
    /**
     * Lets the VAO know if you should use the WebGL extension or the native methods.
     * Some devices behave a bit funny when using the newer extensions (im looking at you ipad 2!)
     * If you find on older devices that things have gone a bit weird then set this to true.
     * @static
     * @property {Boolean} FORCE_NATIVE
     */
    public static FORCE_NATIVE = false;

}




