import { Point, ObservablePoint, Rectangle } from '../math';
import { sign, TextureCache, hex2rgb } from '../utils';
import { BLEND_MODES } from '../const';
import Texture from '../texture/Texture';
import Container from './Container';

import { DisplayObject } from "./DisplayObject";
import { CanvasRenderer } from '../renderers/CanvasRenderer';
import { SCALE_MODES } from '../const';
import { WebglRenderer } from '../renderers/WebglRenderer';

const indices = new Uint16Array([0, 1, 2, 0, 2, 3]);
/**
 * Sprite
 * @class
 * @extends Container
 */
export default class Sprite extends Container {
    /**
     * 
     * @default BLEND_MODES.NORMAL
     */
    _blendMode: BLEND_MODES = BLEND_MODES.NORMAL;
    //
    get blendMode(): BLEND_MODES {
        return this._blendMode
    }
    /**
     * 
     */
    set blendMode(value: BLEND_MODES) {
        // if (value != this._blendMode) this._blendMode = value
    }
    /**
     * 01texture
     * 
     */
    private _anchorTexture: ObservablePoint
    /**
     * 
     */
    private _tint: number;
    /**
     * RGBwebgl
     */
    _tintRGB: number;
    /**
     * _tintcanvas
     */
    _cachedTint: number;
    /**
     * 
     * @member {Texture}
     */
    _texture: Texture;
    /**
     * texture
     */
    _width: number;
    /**
     * texture
     */
    _height: number;
    /**
     * transform
     */
    _transformID: number;
    /**
     * texture
     */
    _textureID: number;
    _transformTrimmedID: number;
    _textureTrimmedID: number;

    /**
     * 
     */
    pluginName: string;

    /**
     * webgl
     */
    _indices: Uint16Array;
    // size: number;
    // start: number;
    _uvs: Float32Array;
    /**
     * 8
     */
    _vertexData: Float32Array;
    /**
     * Trimmedtexture
     */
    _vertexTrimmedData: Float32Array;

    /**
     * @param {Texture} texture
     */
    constructor(texture?: Texture) {
        super();
        this._instanceType = "Sprite";
        this._anchorTexture = new ObservablePoint(
            this._onAnchorUpdate,
            this,
            (texture ? texture.defaultAnchor.x : 0),
            (texture ? texture.defaultAnchor.y : 0)
        );

        this._texture = null;
        this._width = 0;
        this._height = 0;

        this._tint = null;
        this._tintRGB = null;
        this.tint = 0xFFFFFF;
        this._cachedTint = 0xFFFFFF;


        this._uvs = null;

        // call texture setter
        this.texture = texture || Texture.EMPTY;

        this._vertexData = new Float32Array(8);

        this._vertexTrimmedData = null;

        this._transformID = -1;
        this._textureID = -1;

        this._transformTrimmedID = -1;
        this._textureTrimmedID = -1;

        // 
        this._indices = indices;
        // this.size = 4;
        // this.start = 0;

        this.pluginName = 'sprite';
    }

    /**
     * texture
     * 
     * @private
     */
    protected _onTextureUpdate() {
        //
        this._textureID = -1;
        this._textureTrimmedID = -1;
        this._cachedTint = 0xFFFFFF;
        //uv
        if (this._texture.valid) this._uvs = this._texture._uvs.uvsFloat32;
        //
        // so if _width is 0 then width was not set..
        if (this._width) {
            this.scale.x = sign(this.scale.x) * this._width / this._texture.orig.width;
        }

        if (this._height) {
            this.scale.y = sign(this.scale.y) * this._height / this._texture.orig.height;
        }

        //_localBoundsSelf
        this.updateLocalBoundsSelf();
    }

    /**
     * 
     * 
     * @private
     */
    private _onAnchorUpdate() {
        this._transformID = -1;
        this._transformTrimmedID = -1;
        //
        this.updateLocalBoundsSelf();
    }

    /**
     * 
     * padding
     */
    protected updateLocalBoundsSelf() {
        //_localBoundsSelf
        const width = this._texture.orig.width;
        const height = this._texture.orig.height;
        this._localBoundsSelf.x = -width * this.anchorTexture.x;
        this._localBoundsSelf.y = -height * this.anchorTexture.y;
        this._localBoundsSelf.width = width;
        this._localBoundsSelf.height = height;
    }

    /**
     * 0123
     * |   |
     * 6745
     * calculates worldTransform * vertices, store it in vertexData
     */
    calculateVertices() {
        if (this._transformID === this.transform._worldID && this._textureID === this._texture._updateID) {
            return;
        }

        this._transformID = this.transform._worldID;
        this._textureID = this._texture._updateID;

        // set the vertex data
        const texture = this._texture;
        const wt = this.transform.worldMatrix;
        const a = wt.a;
        const b = wt.b;
        const c = wt.c;
        const d = wt.d;
        const tx = wt.tx;
        const ty = wt.ty;
        const vertexData = this._vertexData;
        const trim = texture.trim;
        const orig = texture.orig;
        const anchor = this._anchorTexture;

        let w0 = 0;
        let w1 = 0;
        let h0 = 0;
        let h1 = 0;

        if (trim) {
            // if the sprite is trimmed and is not a tilingsprite then we need to add the extra
            // space before transforming the sprite coords.
            //frametrim
            w1 = trim.x - (anchor._x * orig.width);
            w0 = w1 + trim.width;

            h1 = trim.y - (anchor._y * orig.height);
            h0 = h1 + trim.height;
        }
        else {
            w1 = -anchor._x * orig.width;
            w0 = w1 + orig.width;

            h1 = -anchor._y * orig.height;
            h0 = h1 + orig.height;
        }

        // xy
        vertexData[0] = (a * w1) + (c * h1) + tx;
        vertexData[1] = (d * h1) + (b * w1) + ty;

        // xy
        vertexData[2] = (a * w0) + (c * h1) + tx;
        vertexData[3] = (d * h1) + (b * w0) + ty;

        // xy
        vertexData[4] = (a * w0) + (c * h0) + tx;
        vertexData[5] = (d * h0) + (b * w0) + ty;

        // xy
        vertexData[6] = (a * w1) + (c * h0) + tx;
        vertexData[7] = (d * h0) + (b * w1) + ty;
    }

    /**
     * calculates worldTransform * vertices for a non texture with a trim. store it in vertexTrimmedData
     * This is used to ensure that the true width and height of a trimmed texture is respected
     */
    calculateTrimmedVertices() {
        if (!this._vertexTrimmedData) {
            this._vertexTrimmedData = new Float32Array(8);
        }
        else if (this._transformTrimmedID === this.transform._worldID && this._textureTrimmedID === this._texture._updateID) {
            return;
        }

        this._transformTrimmedID = this.transform._worldID;
        this._textureTrimmedID = this._texture._updateID;

        // lets do some special trim code!
        const texture = this._texture;
        const vertexData = this._vertexTrimmedData;
        const orig = texture.orig;
        const anchor = this._anchorTexture;

        // lets calculate the new untrimmed bounds..
        const wt = this.transform.worldMatrix;
        const a = wt.a;
        const b = wt.b;
        const c = wt.c;
        const d = wt.d;
        const tx = wt.tx;
        const ty = wt.ty;

        const w1 = -anchor._x * orig.width;
        const w0 = w1 + orig.width;

        const h1 = -anchor._y * orig.height;
        const h0 = h1 + orig.height;

        // xy
        vertexData[0] = (a * w1) + (c * h1) + tx;
        vertexData[1] = (d * h1) + (b * w1) + ty;

        // xy
        vertexData[2] = (a * w0) + (c * h1) + tx;
        vertexData[3] = (d * h1) + (b * w0) + ty;

        // xy
        vertexData[4] = (a * w0) + (c * h0) + tx;
        vertexData[5] = (d * h0) + (b * w0) + ty;

        // xy
        vertexData[6] = (a * w1) + (c * h0) + tx;
        vertexData[7] = (d * h0) + (b * w1) + ty;
    }

    /**
    * webgl
    * @private
    * @param {WebglRenderer} renderer
    */
    _renderWebGL(renderer: WebglRenderer) {
        //
        this.calculateVertices();
        renderer.batchManager.setObjectRenderer(renderer.plugins["batch"]);
        renderer.plugins["batch"].render(this);
    }

    /**
    * canvas
    * @private
    * @param {CanvasRenderer} renderer
    */
    _renderCanvas(renderer: CanvasRenderer) {
        renderer.plugins[this.pluginName].render(this);
    }

    /**
     * bounds
     * @private
     */
    _calculateBounds() {
        const trim = this._texture.trim;
        const orig = this._texture.orig;
        //trimtrimorig
        if (!trim || (trim.width === orig.width && trim.height === orig.height)) {
            this.calculateVertices();
            Rectangle.createFromVertexData(this._bounds, this._vertexData);
        }
        else {
            //trimmed bounds...
            this.calculateTrimmedVertices();
            Rectangle.createFromVertexData(this._bounds, this._vertexTrimmedData);
        }
    }

    /**
     * 
     * @param {Rectangle} rect - The output rectangle.
     * @return {Rectangle} The bounds.
     */
    getLocalBounds(rect?: Rectangle): Rectangle {
        //child
        if (this.children.length === 0) {
            if (!rect) {
                rect = DisplayObject.temBounds;
            }
            //_localBoundsSelf
            rect.copy(this._localBoundsSelf)
            return rect;
        }

        return super.getLocalBounds.call(this, rect);
    }


    /**
     * 
     * @param globalPoint 
     * @param isMouseEvent 
     */
    hitTestPoint(globalPoint: Point, isMouseEvent: boolean = false) {
        //
        if (!this.visible) return null
        //mouseChildrenmouseEnablecontainerdisplayObject
        //
        let hitDisplayObject;
        //
        hitDisplayObject = super.hitTestPoint(globalPoint, isMouseEvent);
        //
        if (hitDisplayObject) return hitDisplayObject;
        //
        hitDisplayObject = this.displayObjectHitTestPoint(globalPoint, isMouseEvent);
        if (hitDisplayObject) return hitDisplayObject;
        return null;
    }



    /**
     * 
     */
    destroy() {
        super.destroy();

        //texture
        this._texture.removeEventListener('update', this._onTextureUpdate, this);

        this._anchorTexture = null;
        this._texture = null;
        this._indices = null;
        this._uvs = null;
        this._vertexData = null;
        this._vertexTrimmedData = null;

        //canvas
        if (this["tintedTexture"]) this["tintedTexture"] = null;
    }

    /**
     * Container
     * texture
     * @member {number}
     */
    get width() {
        return Math.abs(this.scale.x) * this._texture.orig.width;
    }

    set width(value) {
        const s = sign(this.scale.x) || 1;

        this.scale.x = s * value / this._texture.orig.width;
        this._width = value;
    }

    /**
     * texture
     * @member {number}
     */
    get height() {
        return Math.abs(this.scale.y) * this._texture.orig.height;
    }

    set height(value) {
        const s = sign(this.scale.y) || 1;
        this.scale.y = s * value / this._texture.orig.height;
        this._height = value;
    }

    /**
     * 000.50.511
     * @member {ObservablePoint}
     */
    get anchorTexture() {
        return this._anchorTexture;
    }

    set anchorTexture(value: any) {
        this._anchorTexture.copy(value);
    }


    /**
     * @member {Texture}
     */
    get texture() {
        return this._texture === Texture.EMPTY ? null : this._texture;//null
    }

    set texture(value) {
        if (this._texture === value) {
            return;
        }
        //_onTextureUpdate
        if (this._texture != Texture.EMPTY &&
            this._texture != null &&
            !this._texture.baseTexture.hasLoaded) this._texture.removeEventListener('update', this._onTextureUpdate, this);
        //
        this._texture = value || Texture.EMPTY;

        this._textureID = -1;
        this._textureTrimmedID = -1;
        this._cachedTint = 0xFFFFFF;

        if (value) {
            if (value.baseTexture.hasLoaded) {
                this._onTextureUpdate();
            }
            else {
                value.once('update', this._onTextureUpdate, this);//
            }
        } else {
            //624_localBoundsSelf
            this.updateLocalBoundsSelf();
        }
    }

    get tint() {
        return this._tint;
    }
    set tint(value) {
        if (value === this._tint) return;
        this._tint = value;
        this._tintRGB = (value >> 16) + (value & 0xff00) + ((value & 0xff) << 16);
    }

    //
    /**
     * 
     * @param url 
     */
    static fromUrl(url: string): Sprite {
        return new Sprite(Texture.fromUrl(url));
    }

    /**
     * TextureCacheframeId
     * @static
     * @param {string} frameId - The frame Id of the texture in the cache
     * @return {Sprite} A new Sprite using a texture from the texture cache matching the frameId
     */
    static fromFrame(frameId: string): Sprite {
        const texture = TextureCache[frameId];
        if (!texture) {
            // throw new Error(`The frameId "${frameId}" does not exist in the texture cache`);
            console.warn("TextureCacheframeId")
        }
        return new Sprite(texture);
    }
}
