import { mapType } from './mapType';
import { defaultValue } from './defaultValue';
/**
 * Extracts the uniforms uniforms
 * @class
 * @memberof glCore.shader
 * @param gl {WebGLRenderingContext} The current WebGL rendering context
 * @param program {WebGLProgram} The shader program to get the uniforms from
 * @return uniforms {Object} uniform
 * uniform
 * {
 *  type: uniform'float','vec2'
 *	size: uniformData.size,uniform
 *	location: WebGLUniformLocation,uniform
 *	value: 
 * }
 */
export function extractUniforms(gl: WebGLRenderingContext, program: WebGLProgram) {
	var uniforms = {};

	var totalUniforms = gl.getProgramParameter(program, gl.ACTIVE_UNIFORMS);

	for (var i = 0; i < totalUniforms; i++) {
		var uniformData:WebGLActiveInfo = gl.getActiveUniform(program, i);
		var name = uniformData.name.replace(/\[.*?\]/, "");
		var type = mapType(gl, uniformData.type);

		uniforms[name] = {
			type: type,
			size: uniformData.size,
			location: gl.getUniformLocation(program, name),
			value: defaultValue(type, uniformData.size)
		};
	}

	return uniforms;
};

