export interface IBoneData {
    length: number
    name: string
    parent: string
    rotation: number
    x: number
    y: number
    scaleX: number//1
    scaleY: number//1
    shearX: number//0
    shearY: number//0
    transform: TransformType //
    skin: boolean,//falseskin
}

enum TransformType {
    // normal = 0,//
    // onlyTranslation,
    // noRotationOrReflection,
    // noScale,
    // noScaleOrReflection,
    normal = "normal",//
    onlyTranslation = "onlyTranslation",
    noRotationOrReflection = "noRotationOrReflection",
    noScale = "noScale",
    noScaleOrReflection = "noScaleOrReflection",
}