
import { Point } from "././Point";
import { HashObject } from "../HashObject";
import { DEG_TO_RAD, cos, sin, RAD_TO_DEG, PI_2 } from "../const";
import Transform from "./Transform";

/**
 * 2
 * @class Matrix
 * @extends HashObject
 * @public
 * @since 1.0.0
 */
export class Matrix extends HashObject {
    /**
     * @property a
     * @type {number}
     * @public
     * @default 1
     * @since 1.0.0
     */
    public a: number = 1;
    /**
     * @property b
     * @public
     * @since 1.0.0
     * @type {number}
     */
    public b: number = 0;
    /**
     * @property c
     * @type {number}
     * @public
     * @since 1.0.0
     */
    public c: number = 0;
    /**
     * @property d
     * @type {number}
     * @public
     * @since 1.0.0
     */
    public d: number = 1;
    /**
     * @property tx
     * @type {number}
     * @public
     * @since 1.0.0
     */
    public tx: number = 0;
    /**
     * @property ty
     * @type {number}
     * @since 1.0.0
     * @public
     */
    public ty: number = 0;

    //
    public array = null;
    /**
     * 
     * @method Matrix
     * @param {number} a
     * @param {number} b
     * @param {number} c
     * @param {number} d
     * @param {number} tx
     * @param {number} ty
     * @public
     * @since 1.0.0
     */
    public constructor(a: number = 1, b: number = 0, c: number = 0, d: number = 1, tx: number = 0, ty: number = 0) {
        super();
        let s = this;
        s._instanceType = "Matrix";
        s.a = a;
        s.b = b;
        s.c = c;
        s.d = d;
        s.tx = tx;
        s.ty = ty;
    }

    /**
     * 
     * @method clone
     * @since 1.0.0
     * @public
     * @return {Matrix}
     */
    public clone(): Matrix {
        let s = this;
        return new Matrix(s.a, s.b, s.c, s.d, s.tx, s.ty);
    }

    /**
     * 
     * @param matrix 
     */
    copy(matrix: Matrix | any) {
        this.a = matrix.a;
        this.b = matrix.b;
        this.c = matrix.c;
        this.d = matrix.d;
        this.tx = matrix.tx;
        this.ty = matrix.ty;

        return this;
    }


    /**
     * 
     * @method transformPoint
     * @param {number} x
     * @param {number} y
     * @param {Point} nullPointnullPoint
     * @return {Point}
     * @public
     * @since 1.0.0
     */
    public transformPoint(x: number, y: number, bp: Point = null): Point {
        let s = this;
        if (!bp) {
            bp = new Point();
        }
        bp.x = x * s.a + y * s.c + s.tx;
        bp.y = x * s.b + y * s.d + s.ty;
        return bp
    };

    /**
     * Get a new position with the inverse of the current transformation applied.
     * Can be used to go from the world coordinate space to a child's coordinate space. (e.g. input)
     * 
     * @param {number} x
     * @param {number} y
     * @param {Point} nullPointnullPoint
     * @return {Point}
     */
    public transformPointInverse(x: number, y: number, bp: Point = null): Point {
        let s = this;
        if (!bp) {
            bp = new Point();
        }

        const id = 1 / ((this.a * this.d) + (this.c * -this.b));

        bp.x = (this.d * id * x) + (-this.c * id * y) + (((this.ty * this.c) - (this.tx * this.d)) * id);
        bp.y = (this.a * id * y) + (-this.b * id * x) + (((-this.ty * this.a) + (this.tx * this.b)) * id);

        return bp;
    }

    /**
     * 
     * @method setFrom
     * @param {Matrix} mtx
     * @public
     * @since 1.0.0
     */
    public setFrom(mtx: Matrix): void {
        let s = this;
        s.a = mtx.a;
        s.b = mtx.b;
        s.c = mtx.c;
        s.d = mtx.d;
        s.tx = mtx.tx;
        s.ty = mtx.ty;
    }

    /**
     * 
     * @method identity
     * @public
     * @since 1.0.0
     */
    public identity(): void {
        let s = this;
        s.a = s.d = 1;
        s.b = s.c = s.tx = s.ty = 0;
    }

    /**
     * 
     * @method invert
     * @return {Matrix}
     * @since 1.0.0
     * @public
     */
    public invert(): Matrix {
        let s = this;
        let a = s.a;
        let b = s.b;
        let c = s.c;
        let d = s.d;
        let tx = s.tx;
        let ty = s.ty;

        if (b == 0 && c == 0) {
            if (a == 0 || d == 0) {
                s.a = s.d = s.tx = s.ty = 0;
            }
            else {
                a = s.a = 1 / a;
                d = s.d = 1 / d;
                s.tx = -a * tx;
                s.ty = -d * ty;
            }
            return s;
        }
        let determinant = a * d - b * c;
        if (determinant == 0) {
            s.identity();
            return s;
        }
        determinant = 1 / determinant;
        let k = s.a = d * determinant;
        b = s.b = -b * determinant;
        c = s.c = -c * determinant;
        d = s.d = a * determinant;
        s.tx = -(k * tx + c * ty);
        s.ty = -(b * tx + d * ty);
        return s;
    }

    /**
     * ,
     * @method createBox
     * @param {number} x
     * @param {number} y
     * @param {number} scaleX
     * @param {number} scaleY
     * @param {number} rotation 
     * @param {number} skewX 
     * @param {number} skewY 
     * @param {number} ax
     * @param {number} ay
     * @since 1.0.0
     * @public
     */
    public createBox(x: number, y: number, scaleX: number, scaleY: number, rotation: number, skewX: number, skewY: number, ax: number, ay: number): void {
        let s = this;
        if (rotation != 0) {
            skewX = skewY = rotation % 360;
        } else {
            skewX %= 360;
            skewY %= 360;
        }
        if ((skewX == 0) && (skewY == 0)) {
            s.a = scaleX;
            s.b = s.c = 0;
            s.d = scaleY;
        } else {
            skewX *= DEG_TO_RAD;
            skewY *= DEG_TO_RAD;
            let u = cos(skewX);
            let v = sin(skewX);
            if (skewX == skewY) {
                s.a = u * scaleX;
                s.b = v * scaleX;
            }
            else {
                s.a = cos(skewY) * scaleX;
                s.b = sin(skewY) * scaleX;
            }
            s.c = -v * scaleY;
            s.d = u * scaleY;
        };
        s.tx = x + ax - (ax * s.a + ay * s.c);
        s.ty = y + ay - (ax * s.b + ay * s.d);
    }

    /**
     * 
     * @method prepend
     * @public
     * @since 1.0.0
     * @param {Matrix} mtx
     */
    public prepend = function (mtx: Matrix): void {
        let s = this;
        let a = mtx.a;
        let b = mtx.b;
        let c = mtx.c;
        let d = mtx.d;
        let tx = mtx.tx;
        let ty = mtx.ty;
        let a1 = s.a;
        let c1 = s.c;
        let tx1 = s.tx;
        s.a = a * a1 + c * s.b;
        s.b = b * a1 + d * s.b;
        s.c = a * c1 + c * s.d;
        s.d = b * c1 + d * s.d;
        s.tx = a * tx1 + c * s.ty + tx;
        s.ty = b * tx1 + d * s.ty + ty;
        return this
    };
    /**
    * Appends the given Matrix to this Matrix.
    *
    * @param {Matrix} matrix - The matrix to append.
    * @return {Matrix} This matrix. Good for chaining method calls.
    */
    public append(matrix: Matrix) {
        const a1 = this.a;
        const b1 = this.b;
        const c1 = this.c;
        const d1 = this.d;

        this.a = (matrix.a * a1) + (matrix.b * c1);
        this.b = (matrix.a * b1) + (matrix.b * d1);
        this.c = (matrix.c * a1) + (matrix.d * c1);
        this.d = (matrix.c * b1) + (matrix.d * d1);

        this.tx = (matrix.tx * a1) + (matrix.ty * c1) + this.tx;
        this.ty = (matrix.tx * b1) + (matrix.ty * d1) + this.ty;

        // return this;
    }

    /**
     * 
     * @method isEqual
     * @static
     * @public
     * @since 1.0.0
     * @param {Matrix} m1
     * @param {Matrix} m2
     * @return {boolean}
     */
    public static isEqual(m1: Matrix, m2: Matrix): boolean {
        return m1.tx == m2.tx && m1.ty == m2.ty && m1.a == m2.a && m1.b == m2.b && m1.c == m2.c && m1.d == m2.d;
    }
    public concat(mtx: Matrix): void {
        let s = this;
        let a = s.a, b = s.b, c = s.c, d = s.d,
            tx = s.tx, ty = s.ty;
        let ma = mtx.a, mb = mtx.b, mc = mtx.c, md = mtx.d,
            mx = mtx.tx, my = mtx.ty;
        s.a = a * ma + b * mc;
        s.b = a * mb + b * md;
        s.c = c * ma + d * mc;
        s.d = c * mb + d * md;
        s.tx = tx * ma + ty * mc + mx;
        s.ty = tx * mb + ty * md + my;
    }
    /**
     * 
     * @method rotate
     * @param angle 
     * @since 1.0.3
     * @public
     */
    public rotate(angle: number): void {
        let s = this;
        let sin = Math.sin(angle), cos = Math.cos(angle),
            a = s.a, b = s.b, c = s.c, d = s.d,
            tx = s.tx, ty = s.ty;
        s.a = a * cos - b * sin;
        s.b = a * sin + b * cos;
        s.c = c * cos - d * sin;
        s.d = c * sin + d * cos;
        s.tx = tx * cos - ty * sin;
        s.ty = tx * sin + ty * cos;
    }

    /**
     * 
     * @method scale
     * @param {Number} sx  x 
     * @param {Number} sy  y 
     * @since 1.0.3
     * @public
     */
    public scale(sx: number, sy: number): void {
        let s = this;
        s.a *= sx;
        s.d *= sy;
        s.c *= sx;
        s.b *= sy;
        s.tx *= sx;
        s.ty *= sy;
    }
    /**
     *  x  y  dx  dy 
     * @method translate
     * @public
     * @since 1.0.3
     * @param {Number} dx  x 
     * @param {Number} dy  y 
     */
    public translate(dx: number, dy: number) {
        let s = this;
        s.tx += dx;
        s.ty += dy;
    }


    public set(a, b, c, d, tx, ty) {
        this.a = a;
        this.b = b;
        this.c = c;
        this.d = d;
        this.tx = tx;
        this.ty = ty;

        return this;
    }

    /**
     * ,,
     * x,y,tx,ty
     * scalea,d
     * skew
     */
    public getRotation() {
        return Math.round(Math.atan2(this.b, this.a) * RAD_TO_DEG);
    }

    /**
     * Creates an array from the current Matrix object.glslmat3,transpose;
     *
     * @param {boolean} transpose - Whether we need to transpose the matrix or not
     * @param {Float32Array} [out=new Float32Array(9)] - If provided the array will be assigned to out
     * @return {number[]} the newly created array which contains the matrix
     */
    public toArray(transpose = false, out?) {
        if (!this.array) {
            this.array = new Float32Array(9);
        }

        const array = out || this.array;

        if (transpose) {
            array[0] = this.a;
            array[1] = this.b;
            array[2] = 0;
            array[3] = this.c;
            array[4] = this.d;
            array[5] = 0;
            array[6] = this.tx;
            array[7] = this.ty;
            array[8] = 1;
        } else {
            array[0] = this.a;
            array[1] = this.c;
            array[2] = this.tx;
            array[3] = this.b;
            array[4] = this.d;
            array[5] = this.ty;
            array[6] = 0;
            array[7] = 0;
            array[8] = 1;
        }

        return array;
    }
    /**
     * tansform
     * @param transform 
     */
    decompose(transform: Transform) {
        const a = this.a;
        const b = this.b;
        const c = this.c;
        const d = this.d;
        //
        const skewX = -Math.atan2(-c, d);
        const skewY = Math.atan2(b, a);

        const delta = Math.abs(skewX + skewY);
        //
        if (delta < 0.00001 || Math.abs(PI_2 - delta) < 0.00001) {
            transform.rotation = skewY;
            //
            if (a < 0 && d >= 0) {
                transform.rotation += (transform.rotation <= 0) ? Math.PI : -Math.PI;
            }
            transform.skew.x = transform.skew.y = 0;
        }
        else {
            transform.rotation = 0;
            transform.skew.x = skewX;
            transform.skew.y = skewY;
        }

        //
        transform.scale.x = Math.sqrt((a * a) + (b * b));
        transform.scale.y = Math.sqrt((c * c) + (d * d));

        //
        transform.position.x = this.tx;
        transform.position.y = this.ty;

        return transform;
    }
    /**
     * A default (identity) matrix
     *
     * @static
     * @const
     */
    public static get IDENTITY() {
        return new Matrix();
    }

    /**
     * A temp matrix
     *
     * @static
     * @const
     */
    public static get TEMP_MATRIX() {
        return new Matrix();
    }

    destroy(): void {
    }
}